<template>
  <v-layout align-start>
    <v-flex>
      <v-toolbar flat color="white">
        <v-toolbar-title>Ingresos</v-toolbar-title>
        <v-divider class="mx-2" inset vertical></v-divider>
       <v-btn class="success" @click="mostrarExportar">
          Fechas
        </v-btn>
        <v-btn @click="crearPDF()">
          <v-icon>print</v-icon>
        </v-btn>
        <v-spacer></v-spacer>
        <v-text-field
          v-if="verNuevo==0"
          class="text-xs-center"
          v-model="search"
          append-icon="search"
          label="Búsqueda"
          single-line
          hide-details
        ></v-text-field>
        <v-spacer></v-spacer>
        <v-btn color="primary" v-if="verNuevo==0" @click="mostrarNuevo()" dark class="mb-2">Nuevo</v-btn>
        <v-dialog v-model="ExportarModal" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="headline">Seleccione el rango de fechas:</span>
              </v-card-title>
              <v-card-text>
                <v-container grid-list-md>
                  <v-layout wrap>
                          <v-flex xs12 sm6 md6>
                            <v-dialog
                              ref="dialog1"
                              v-model="modal1"
                              :return-value.sync="finicio"
                              persistent
                              width="290px"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                  <v-text-field
                                    v-model="finicio"
                                    label="Fecha de inicio"
                                    prepend-icon="event"
                                    readonly
                                    v-bind="attrs"
                                    v-on="on"
                                  ></v-text-field>
                                </template>
                                <v-date-picker v-model="finicio" scrollable>
                                  <v-spacer></v-spacer>
                                  <v-btn text color="primary" @click="modal1 = false">Cancelar</v-btn>
                                  <v-btn
                                    text
                                    color="success"
                                    @click="$refs.dialog1.save(finicio)"
                                  >Aceptar</v-btn>
                                </v-date-picker>
                              </v-dialog>
                          </v-flex>
                          <v-flex xs12 sm6 md6>
                            <v-dialog
                              ref="dialog"
                              v-model="modal"
                              :return-value.sync="ffin"
                              persistent
                              width="290px"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                  <v-text-field
                                    v-model="ffin"
                                    label="Fecha de fin"
                                    prepend-icon="event"
                                    readonly
                                    v-bind="attrs"
                                    v-on="on"
                                  ></v-text-field>
                                </template>
                                <v-date-picker v-model="ffin" scrollable>
                                  <v-spacer></v-spacer>
                                  <v-btn text color="primary" @click="modal = false">Cancelar</v-btn>
                                  <v-btn
                                    text
                                    color="success"
                                    @click="$refs.dialog.save(ffin)"
                                  >Aceptar</v-btn>
                                </v-date-picker>
                              </v-dialog>
                          </v-flex>
                            <v-flex xs12 sm12 md12 v-show="valida">
                              <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
                            </v-flex>
                  </v-layout>
                </v-container>
              </v-card-text>  
              <v-card-actions>
                  <v-btn color="primary" @click="filtrarFechas()">Buscar</v-btn>
                   <v-btn color="secundary" @click="ocultarExportar()">Cancelar</v-btn>
              </v-card-actions>
            </v-card>
        </v-dialog >
        <v-dialog v-model="dialog" max-width="1000px">
          <v-card>
            <v-card-title>
              <span class="headline">Seleccione un artículo</span>
            </v-card-title>
            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12 sm12 md12 lg12 xl12>
                    <v-text-field
                      v-model="texto"
                      @keyup.enter="listarArticulosnombres()"
                      class="text-xs-center"
                      append-icon="search"
                      label="Búsqueda"
                    ></v-text-field>
                    <template>
                      <v-data-table
                        :headers="cabeceraArticulos"
                        :items="articulos"
                        class="elevation-1"
                      >
                        <template v-slot:items="props">
                          <td class="justify-center layout px-0">
                            <v-icon small class="mr-2" @click="agregarDetalle(props.item)">add</v-icon>
                          </td>
                          <td class="blue--text">{{ props.item.codigoBarras }}</td>
                          <td>{{ props.item.nombre }}</td>
                           <td>{{ props.item.nombreComercial }}</td>
                            <td class="green--text">{{ props.item.codigoLaboratorio.abreviatura }}</td>
                          <td>{{ props.item.stock }}</td>
                           
                          <td>${{ props.item.costoNeto }}</td>
                        
                          <td>
                            <div v-if="props.item.estado">
                              <span class="blue--text">Activo</span>
                            </div>
                            <div v-else>
                              <span class="red--text">Inactivo</span>
                            </div>
                          </td>
                        </template>
                      </v-data-table>
                    </template>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="close">Cancelar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="adModal" max-width="290">
          <v-card>
            <v-card-title class="headline" v-if="adAccion==1">Activar Item</v-card-title>
            <v-card-title class="headline" v-if="adAccion==2">Desactivar Item</v-card-title>
            <v-card-text>
              Estás a punto de
              <span v-if="adAccion==1">activar</span>
              <span v-if="adAccion==2">desactivar</span>
              el item {{adNombre}}
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn @click="activarDesactivarCerrar()" color="green darken-1" flat="flat">Cancelar</v-btn>
              <v-btn
                v-if="adAccion==1"
                @click="activar()"
                color="orange darken-4"
                flat="flat"
              >Activar</v-btn>
              <v-btn
                v-if="adAccion==2"
                @click="desactivar()"
                color="orange darken-4"
                flat="flat"
              >Desactivar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
      <v-data-table
        :headers="headers"
        :items="ingresos"
        :search="search"
        class="elevation-1"
        v-if="verNuevo==0"
      >
        <template v-slot:items="props">
          <td class="justify-center layout px-0">
            <v-icon small class="mr-2" @click="verIngreso(props.item)">tab</v-icon>
            <template v-if="props.item.estado">
              <v-icon small @click="activarDesactivarMostrar(2,props.item)">block</v-icon>
            </template>
            <template v-else>
              <v-icon small @click="activarDesactivarMostrar(1,props.item)">check</v-icon>
            </template>
          </td>
          <td>{{ props.item.codigoFarmacia.descripcion }}</td>
          <td>{{ props.item.codigoUsuario.nombres }}</td>
          <td>{{ props.item.codgioPersona.nombres }}</td>
          <td>{{ props.item.descripcion }}</td>
          <td>{{ props.item.codigoTipoComprobante.descripcion }}</td>
          <td>{{ props.item.serieComprobante }}</td>
          <td>{{ props.item.numComprobante }}</td>
          <td>{{formatearFecha(props.item.createdAt)}}</td>
          <td>{{ props.item.impuesto }}</td>
          <td>{{ props.item.total }}</td>
          <td>
            <div v-if="props.item.estado">
              <span class="blue--text">Aceptado</span>
            </div>
            <div v-else>
              <span class="red--text">Anulado</span>
            </div>
          </td>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary" @click="listar()">Resetear</v-btn>
        </template>
      </v-data-table>
      <v-container grid-list-sm class="pa-4 white" v-if="verNuevo">
        <v-layout row wrap>
             <v-flex xs12 sm12 md12 lg12 xl2 class="font-weight-bold green--text" style="font-size:20px">
        <span>Farmacia: {{Farmacia}}</span>
      </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select
              v-model="codigoTipoComprobante"
              :items="tiposcomprobantes"
              label="Tipo Comprobante"
            ></v-select>
          </v-flex>
          <v-flex xs12 sm2 md2 lg2 xl2>
            <v-text-field v-model="serieComprobante" readonly label="Serie Comprobante"></v-text-field>
          </v-flex>
          <v-flex xs12 sm2 md2 lg2 xl2>
            <v-text-field v-model="numComprobante" label="Número Comprobante"></v-text-field>
          </v-flex>
          <v-flex xs12 sm6 md6 lg6 xl6>
            <v-autocomplete :items="personas" v-model="codgioPersona" label="Proveedor"></v-autocomplete>
          </v-flex>
           <v-flex xs12 sm6 md6 lg6 xl6>
            <v-text-field v-model="descripcion" label="Descripcion"></v-text-field>
          </v-flex>
        
          <v-flex xs12 sm8 md8 lg8 x8>
            <v-text-field v-model="codigoBarras" label="Código" @keyup.enter="buscarCodigo()"></v-text-field>
          </v-flex>
          <v-flex xs12 sm2 md2 lg2 xl2>
            <v-btn small fab dark color="teal" @click="mostrarModalArticulos()">
              <v-icon dark>list</v-icon>
            </v-btn>
          </v-flex>
          <v-flex xs12 sm2 md2 lg2 xl2 v-show="errorArticulo">
            <div class="red--text" v-text="errorArticulo"></div>
          </v-flex>
          <v-flex xs12 sm12 md12 lg12 xl12>
            <template>
              <v-data-table
                :headers="cabeceraDetalles"
                :items="detalles"
                hide-actions
                class="elevation-1"
              >
                <template slot="items" slot-scope="props">
                  <td>
                    <v-icon small class="mr-2 " @click="eliminarDetalle(detalles,props.item)">delete</v-icon>
                  </td>
                  <td class="text-xs-center blue--text">{{ props.item.producto }}</td>
                  <td class="text-xs-center">
                    <v-text-field v-model="props.item.cantidad" type="number"></v-text-field>
                  </td>
                  <td class="text-xs-center">
                {{ props.item.fracciones*props.item.cantidad}}
                  </td>
                  <td class="text-xs-center">
                    <v-text-field v-model="props.item.costoNeto" type="number"></v-text-field>
                  </td>
                   <td>
                            <div v-if="props.item.iva">
                             {{props.item.costoNeto*0.12}}

                            </div>
                            <div v-else>
                              0
                            </div>
                          </td>
                  <td class="text-xs-right">
                    
                  
                              {{ (props.item.cantidad * props.item.costoNeto)  }}
                           
                    
                    </td>
                </template>
                <template slot="no-data">
                  <h3>No hay artículos agregados al detalle.</h3>
                </template>
              </v-data-table>
              <v-flex class="text-xs-right">
                <strong>Total Parcial:</strong>
                $
                {{totalParcial=(caltularSubtotal).toFixed(5)}}
              </v-flex>
              <v-flex class="text-xs-right">
                <strong>Total Impuesto:</strong>
                $ {{totalImpuesto=(calcularIva).toFixed(5)}}
              </v-flex>
              <v-flex class="text-xs-right">
                <strong>Total:</strong>
                $ {{total=calcularTotal.toFixed(5)}}
              </v-flex>
            </template>
          </v-flex>
          <v-flex xs12 sm12 md12 v-show="valida">
            <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
          </v-flex>
          <v-flex xs12 sm12 md12 lg12 xl12>
            <v-btn color="blue darken-1" flat @click.native="ocultarNuevo()">Cancelar</v-btn>
            <v-btn color="success" v-if="verDetalle==0" @click.native="guardar()">Guardar</v-btn>
          </v-flex>
        </v-layout>
      </v-container>
    </v-flex>
  </v-layout>
</template>
<script>

import moment from "moment"
import axios from "axios";
import jsPDF from "jspdf";
import autoTable from "jspdf-autotable";
import Swal from 'sweetalert2'
export default {
  data() {
    return {
      finicio:"",
      ffin:"",
      modal:false,
      modal1:false,
      ExportarModal:0,
      dialog: false,
      search: "",
      ingresos: [],
      headers: [
        { text: "Opciones", value: "opciones", sortable: false },    
         { text: "Farmacia", value: "codigoFarmacia.descripcion" },

        { text: "Usuario", value: "codigoUsuario.nombres", sortable: true },
        { text: "Proveedor", value: "codgioPersona.nombres", sortable: true },
        { text: "Descripcion", value: "descripcion", sortable: true },
        {
          text: "Tipo Comprobante",
          value: "codigoTipoComprobante.descripcion",
          sortable: true
        },
        {
          text: "Serie comprobante",
          value: "serieComprobante",
          sortable: false
        },
        {
          text: "Número comprobante",
          value: "numComprobante",
          sortable: false
        },
        { text: "Fecha", value: "createdAt", sortable: false },
        { text: "Impuesto", value: "impuesto", sortable: false },
        { text: "Total", value: "total", sortable: false },
        { text: "Estado", value: "estado", sortable: false }
      ],
      _id: "",
      codgioPersona: "",
      personas: [],
      codigoTipoComprobante: "",
      tiposcomprobantes: [],
      serieComprobante: "001",
      numComprobante: "",
     
      codigo: "",
      cabeceraDetalles: [
        { text: "Borrar", value: "borrar", sortable: false },
        { text: "Producto", value: "producto", sortable: false },
        { text: "Cantidad", value: "cantidad", sortable: false },
        { text: "Fraccion", value: "fracciones", sortable: false },
        { text: "Costo Neto", value: "costoNeto", sortable: false },
        { text: "IVA?", value: "iva", sortable: false },
        { text: "Sub Total", value: "subtotal", sortable: false }
      ],
      detalles: [],
      verNuevo: 0,
      errorArticulo: null,
      total: 0.0,
      totalParcial: 0.0,
      totalImpuesto: 0.0,
      articulos: [],
      texto: "",
      cabeceraArticulos: [
        { text: "Seleccionar", value: "seleccionar", sortable: false },
        { text: "Código", value: "codigo", sortable: false },
        { text: "Nombre", value: "nombre", sortable: true },
        { text: "Nombre Comercial", value: "nombreComercial", sortable: true },
        { text: "Laboratorio", value: "codigoLaboratorio.abreviatura", sortable: true },
        { text: "Stock", value: "stock", sortable: false },
        { text: "Precio Venta", value: "costoNeto", sortable: false },
        { text: "Estado", value: "estado", sortable: false }
      ],
      verDetalle: 0,
      valida: 0,
      validaMensaje: [],
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: "",
      codigoBarras: "",
      iva:0,
      descripcion:'',
      Farmacia:"",
      farmacias: [],
  
    
    };
  },
  computed: {
    calcularTotal: function() {
      let resultado = 0.0;
      for (var i = 0; i < this.detalles.length; i++) {
        if(this.detalles[i].iva==1){
             resultado =
          resultado + (this.detalles[i].cantidad * this.detalles[i].costoNeto*0.12) + (this.detalles[i].cantidad * this.detalles[i].costoNeto);
        }else{
        resultado =
          resultado + this.detalles[i].cantidad * this.detalles[i].costoNeto;
        }
      }
      return resultado;
    },
    caltularSubtotal: function() {
      let resultado = 0.0;
      for (var i = 0; i < this.detalles.length; i++) {
        
        resultado =
          resultado + this.detalles[i].cantidad * this.detalles[i].costoNeto;
        
      }
      return resultado;
    },
    calcularIva: function() {
      let resultado = 0.0;
      for (var i = 0; i < this.detalles.length; i++) {
        if(this.detalles[i].iva==1){
             resultado =
          resultado + (this.detalles[i].cantidad * this.detalles[i].costoNeto*0.12) ;
        }
      }
      return resultado;
    },
   
  },
  
  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  created() {
    this.listar();
    this.selectPersona();
    this.selectTipocomprobante();
    this.selectFarmacias();
     this.ObtenerFa();
  },
  methods: {
    formatearFecha(value) {
      if (value) {
        return moment(String(value)).format('MM/DD/YYYY hh:mm')
      }
      },
    ObtenerFa() {
      let me = this;
      let ArrayT = [];

      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;
      if (codigoFarmacia == undefined) {
        me.Farmacia = "ADMINISTRADOR";
      } else {
        axios
          .get("farmacia/query?_id=" + codigoFarmacia, configuracion)
          .then(function(response) {
            me.farmacias = response.data;
            me.Farmacia = me.farmacias.descripcion;
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    selectFarmacias() {
      let me = this;
      let ArrayT = [];
      let header = { Token: this.$store.state.token };
      let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;
      let configuracion = { headers: header };
       if (codigoFarmacia == undefined) {
        axios
          .get("farmacia/list", configuracion)
          .then(function(response) {
             ArrayT = response.data;
          ArrayT.map(function(x) {
            me.farmacias.push({ text: x.descripcion, value: x._id });
          });
        })
          .catch(function(error) {
            console.log(error);
          });
      } else {
      
      axios
        .get("farmacia/query?_id="+codigoFarmacia, configuracion)
        .then(function(response) {
           me.farmacias.push({ text: response.data.descripcion, value: response.data._id })
         
        })
        .catch(function(error) {
          console.log(error);
        });}
    },
    selectPersona() {
      let me = this;
      let personaArray = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("persona/listProveedor", configuracion)
        .then(function(response) {
          personaArray = response.data;
          personaArray.map(function(x) {
            me.personas.push({ text: x.nombres, value: x._id });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    selectTipocomprobante() {
      let me = this;
      let tipocomprobanteArray = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("tipocomprobante/list", configuracion)
        .then(function(response) {
          tipocomprobanteArray = response.data;
          tipocomprobanteArray.map(function(x) {
            me.tiposcomprobantes.push({ text: x.descripcion, value: x._id });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    buscarCodigo() {
      let me = this;
      me.errorArticulo = null;
       let header1 = { Token: this.$store.state.token };
      let configuracion1 = { headers: header1 };
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
        let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;
     let codigoBarr = this.codigoBarras;

     if (codigoFarmacia == undefined) {
        
      axios
        .get("productos/query?codigoBarras=" + this.codigoBarras, configuracion)
        .then(function(response) {
          me.agregarDetalle(response.data);
        })
        .catch(function(error) {
          me.errorArticulo = "No existe el artículo.";
        });
      } else {
       //Buscar el inventario a traves del codigo farmacia...
        axios
          .get("inventario/query?_id=" + codigoFarmacia, configuracion1) //#1
          .then(function(codigoInvenatio) {
           
            //Buscar los productos que pertenecen al inventario...
            axios
        .get("productos/queryB?valor="+codigoInvenatio.data._id+"&codigoBarras="+codigoBarr, configuracion)
        .then(function(response) {
          me.agregarDetalle(response.data);
        })
        .catch(function(error) {
          me.errorArticulo = "No existe el artículo.";
        });

          })
          .catch(function(error) {
            console.log(error);
          });
      
      }




    },
    agregarDetalle(data) {
      this.errorArticulo = null;
      if (this.encuentra(data._id) == true) {
        this.errorArticulo = "El artículo ya ha sido agregado.";
      } else {
         
        this.detalles.push({
          _id: data._id,
          producto: data.nombreComercial,
          cantidad: 1,
          iva:data.iva,
          fracciones:data.fraccionCaja,
          costoNeto: data.costoNeto,
         
        });
        this.codigoBarras = "";
      }
    },
    encuentra(id) {
      let sw = 0;
      for (var i = 0; i < this.detalles.length; i++) {
        if (this.detalles[i]._id == id) {
          sw = true;
        }
      }
      return sw;
    },
    eliminarDetalle(arr, item) {
      let i = arr.indexOf(item);
      if (i != -1) {
        arr.splice(i, 1);
      }
    },
    listarArticulos() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("productos/list?valor=" + this.texto, configuracion)
        .then(function(response) {
          me.articulos = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    listarArticulosnombres() {

       let me = this;
      const codigo = "";
      let text = this.texto;
      let header1 = { Token: this.$store.state.token };
      let configuracion1 = { headers: header1 };
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;

      if (codigoFarmacia == undefined) {
        axios
        .get("productos/listtotal?valor=" + this.texto, configuracion)
        .then(function(response) {
          me.articulos = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
      } else {
        //Buscar el inventario a traves del codigo farmacia...
        axios
          .get("inventario/query?_id=" + codigoFarmacia, configuracion1) //#1
          .then(function(codigoInvenatio) {
           
            //Buscar los productos que pertenecen al inventario...
            axios
              .get(
                "productos/list?valor="+ codigoInvenatio.data._id+"&producto="+text,
                configuracion
              )
              .then(function(response) {
                me.articulos = response.data;
              })
              .catch(function(error) {
                console.log(error);
              });
          })
          .catch(function(error) {
            console.log(error);
          });
      }


     
      
    },
    mostrarModalArticulos() {
      this.dialog = 1;
    },
    listarDetalles(id) {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("ingresos/query?_id=" + id, configuracion)
        .then(function(response) {
          me.detalles = response.data.detalles;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    verIngreso(item) {
      this.limpiar();
      
      this.codigoTipoComprobante = item.codigoTipoComprobante._id;
     this.descripcion=item.descripcion;
      this.serieComprobante = item.serieComprobante;
      this.numComprobante = item.numComprobante;
      this.codgioPersona = item.codgioPersona._id;
      this.impuesto = item.impuesto;
      
      this.listarDetalles(item._id);
      this.verNuevo = 1;
      this.verDetalle = 1;
    },
    listar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;
      let configuracion = { headers: header };
      if (codigoFarmacia == undefined) {
         axios
        .get("ingresos/listtotal", configuracion)
        .then(function(response) {
          me.ingresos = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
      } else {
       
       axios
              .get(
                "ingresos/list?valor=" + codigoFarmacia,
                configuracion
              )
              .then(function(response) {
               me.ingresos = response.data;
              })
              .catch(function(error) {
                console.log(error);
              });
      }



    },
    limpiar() {
      this._id = "";
      this.codigoTipoComprobante = "";
      this.serieComprobante = "";
      this.numComprobante = "";
      this.impuesto = 0.12;
      this.codigoBarras = "";
      this.total = 0;
      this.totalParcial = 0;
      this.totalImpuesto = 0;
      this.detalles = [];
      this.verNuevo = 0;
      this.valida = 0;
      this.validaMensaje = [];
      this.verDetalle = 0;
    },
    validar() {
      this.valida = 0;
      this.validaMensaje = [];
      if (!this.personas) {
        this.validaMensaje.push("Seleccione un proveedor.");
      }
      if (!this.tiposcomprobantes) {
        this.validaMensaje.push("Seleccione un tipo de comprobante.");
      }
      if (!this.numComprobante) {
        this.validaMensaje.push("Ingrese el número del comprobante.");
      }
      
      if (this.detalles.length <= 0) {
        this.validaMensaje.push("Ingrese al menos un artículo al detalle");
      }
      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },
    mostrarNuevo() {
      this.verNuevo = 1;
    },
    ocultarNuevo() {
      this.verNuevo = 0;
      this.limpiar();
    },
    guardar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      if (this.validar()) {
        return;
      }
      //Código para guardar
      axios
        .post(
          "ingresos/add",
          {
            codgioPersona: this.codgioPersona,
            codigoUsuario: this.$store.state.usuario.codigoUsuario,
            codigoFarmacia:this.$store.state.usuario.codigoFarmacia,
            codigoTipoComprobante: this.codigoTipoComprobante,
            serieComprobante: this.serieComprobante,
            descripcion:this.descripcion,
            numComprobante: this.numComprobante,
            impuesto: this.calcularIva,
            total: this.total,
            detalles: this.detalles
          },
          configuracion
        )
        .then(function(response) {
          me.limpiar();
          me.close();
          me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    activarDesactivarMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.numComprobante;
      this.adId = item._id;
      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },
    activarDesactivarCerrar() {
      this.adModal = 0;
    },
    activar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .put("ingresos/activate", { _id: this.adId }, configuracion)
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    desactivar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .put("ingresos/deactivate", { _id: this.adId }, configuracion)
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    close() {
      this.dialog = false;
    },

    validarF() {
      this.valida = 0;
      this.validaMensaje = [];
      
      var Ainicio = this.finicio.slice(0,4)
      
      var Minicio = this.finicio.slice(5,7)
     
      var Dinicio = this.finicio.slice(8,10)
      
      
      var Afin = this.ffin.slice(0,4)
      
      var Mfin = this.ffin.slice(5,7)
    
      var Dfin = this.ffin.slice(8,10)
      if(parseInt(Ainicio)<=parseInt(Afin) && parseInt(Minicio)<=parseInt(Mfin) && parseInt(Dinicio)<=parseInt(Dfin)){
         
      }else{
        this.validaMensaje.push("Debe ingresar una fecha de incio.");
      }
      if(!this.finicio) {
        this.validaMensaje.push("Debe ingresar una fecha de incio.");
      }
      if (!this.ffin) {
        this.validaMensaje.push("Debe ingresar una fecha de fin.");
      }
       if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },
    filtrarFechas(){
        let me = this;
          let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;
     if (this.validarF()) {
        return;
      }
       if (codigoFarmacia == undefined) {
        axios
          .get("ingresos/listporfechasad?fechainicio="+this.finicio+"&fechafin="+this.ffin+"")
         .then(function(response) {
          me.ingresos = response.data;
          me.ocultarExportar()
         })
        .catch(function(error) {
          console.log(error);
        });
      } 
      else {
         axios
          .get("ingresos/listporfechas?valor=" + codigoFarmacia+"&fechainicio="+this.finicio+"&fechafin="+this.ffin+"")
         .then(function(response) {
          me.ingresos = response.data;
          me.ocultarExportar()
         })
        .catch(function(error) {
          console.log(error);
        });
        }
          
    },
    mostrarExportar(item){
      this.ExportarModal=1;
    },
    ocultarExportar(item){
      this.finicio="";
      this.ffin="";
      this.validaMensaje=[]
      this.ExportarModal=0;
    },
    crearPDF() {
       let me = this;
      var img =
        "data:image/jpeg;base64,";

     
      
      var columns = [
        { title: "Farmacia", dataKey:"codigoFarmacia" },
        { title: "Usuario", dataKey:"codigoUsuario" },
        { title: "Proveedor", dataKey:"codgioPersona"},
        { title: "Descripcion", dataKey:"descripcion" },
        { title: "Tipo Comprobante",dataKey:"codigoTipoComprobante"},
        { title: "Serie comprobante",dataKey:"serieComprobante"},
        { title: "Número comprobante",dataKey:"numComprobante"},
        { title: "Fecha", dataKey: "createdAt"},
        { title: "Impuesto", dataKey: "impuesto"},
        { title: "Total", dataKey: "total"}
        ]
        
      var rows = [];
      this.ingresos.map(function(x) {
        rows.push({
          codigoFarmacia: x.codigoFarmacia.descripcion,
          codigoUsuario: x.codigoUsuario.nombres,
          codgioPersona: x.codgioPersona.nombres,
          descripcion: x.descripcion,
          codigoTipoComprobante: x.codigoTipoComprobante.descripcion,
          serieComprobante: x.serieComprobante,
          numComprobante: x.numComprobante,
          createdAt:me.formatearFecha(x.createdAt),
          impuesto: x.impuesto,
          total: x.total
          
        });
      });
     
      var doc = new jsPDF();
      doc.autoTable(columns, rows, {
        margin: { top: 60 },
        addPageContent: function(data) {
          doc.text("Reporte de ingresos", 80, 20);
          doc.setTextColor("blue");
          doc.text("LINEA DE FARMACIAS TU AHORRO", 60, 30);
          doc.text(me.Farmacia, 85, 40);
          doc.addImage(img, "JPEG", 160, 10, 35, 35);
        }
      });
      doc.save("ReporteIngresos.pdf");
    },
   
  }
};
</script>
