<template>
  <v-layout align-start>
    <v-flex>
      <v-toolbar flat color="white">
        <v-btn @click="crearPDF()">
          <v-icon>print</v-icon>
        </v-btn>
        <v-toolbar-title>Articulos</v-toolbar-title>
        <v-divider class="mx-2" inset vertical></v-divider>
        
        <v-spacer></v-spacer>
        
        <v-checkbox 
          style="margin-top:15px" 
          color="success"
          v-model="checkbox"
          label="Ver todas las farmacias"
          @change="checkbox_Bandera"
        ></v-checkbox>
       <v-spacer></v-spacer>
        <v-text-field
          class="text-xs-center"
          v-model="search"
          append-icon="search"
          label="Búsqueda"
          single-line
          hide-details
        ></v-text-field>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark class="mb-2" v-on="on">Nuevo</v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>
            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12 sm6 md6>
                    <v-text-field v-model="codigoBarras" label="Codigo de Barra"></v-text-field>
                  </v-flex>

                  <v-flex xs12 sm6 md6>
                    <v-select v-model="iva" :items="ivade" label="Iva"></v-select>
                  </v-flex>
                  <v-flex xs12 sm12 md12>
                    <v-text-field v-model="nombre" label="Producto"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm12 md12>
                    <v-text-field v-model="nombreComercial" label="Nombre comercial"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm12 md12>
                    <v-select v-model="codigoInventario" :items="inventario" label="Inventario"></v-select>
                  </v-flex>
                  <v-flex xs12 sm6 md6>
                    <v-select v-model="codigoCategoria" :items="categoria" label="Categoria"></v-select>
                  </v-flex>
                  <v-flex xs12 sm6 md6>
                    <v-select v-model="codigoLaboratorio" :items="laboratorio" label="Laboratorio"></v-select>
                  </v-flex>
                  <v-flex xs12 sm6 md6>
                    <v-select
                      v-model="codigoPresentacion"
                      :items="presentacion"
                      label="Presentacion"
                    ></v-select>
                  </v-flex>
                  <v-flex xs12 sm6 md6>
                    <v-dialog
                      ref="dialog"
                      v-model="modal"
                      :return-value.sync="fechaCaducidad"
                      persistent
                      width="290px"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                          v-model="fechaCaducidad"
                          label="Fecha de Caducidad"
                          prepend-icon="event"
                          readonly
                          v-bind="attrs"
                          v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker v-model="fechaCaducidad" scrollable>
                        <v-spacer></v-spacer>
                        <v-btn text color="primary" @click="modal = false">Cancelar</v-btn>
                        <v-btn
                          text
                          color="success"
                          @click="$refs.dialog.save(fechaCaducidad)"
                        >Aceptar</v-btn>
                      </v-date-picker>
                    </v-dialog>
                  </v-flex>
                  <v-flex xs12 sm12 md12>
                    <div class="text-center">
                      <v-chip class="ma-2" color="primary">Cantidades</v-chip>
                    </div>
                  </v-flex>

                  <v-flex xs12 sm4 md4>
                    <v-text-field v-model="stock" label="Stock" readonly></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm4 md4>
                    <v-text-field v-model="fraccionCaja" label="Fracciones/Caja"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm4 md4>
                    <v-text-field v-model="fraccionesTotales" readonly label="Fraciones Totales"></v-text-field>
                  </v-flex>

                  <v-flex xs12 sm12 md12>
                    <div class="text-center">
                      <v-chip class="ma-2" color="success">Precios</v-chip>
                    </div>
                  </v-flex>

                  <v-flex xs12 sm2 md2>
                    <v-text-field v-model="costoNeto" label="Costo Neto"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm2 md2>
                    <v-text-field v-model="pvp" label="PVP"></v-text-field>
                  </v-flex>

                  <v-flex xs12 sm2 md2>
                    <v-text-field v-model="precioUni" label="P. Unit."></v-text-field>
                  </v-flex>

                  <v-flex xs12 sm3 md3>
                    <v-text-field v-model="descuento" label="Descuento"></v-text-field>
                  </v-flex>

                  <v-flex xs12 sm12 md12 v-show="valida">
                    <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="close">Cancelar</v-btn>
              <v-btn color="blue darken-1" flat @click="guardar">Guardar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="adModal" max-width="290">
          <v-card>
            <v-card-title class="headline" v-if="adAccion==1">Activar Item</v-card-title>
            <v-card-title class="headline" v-if="adAccion==2">Desactivar Item</v-card-title>
            <v-card-text>
              Estás a punto de
              <span v-if="adAccion==1">activar</span>
              <span v-if="adAccion==2">desactivar</span>
              el item {{adNombre}}
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn @click="activarDesactivarCerrar()" color="green darken-1" flat="flat">Cancelar</v-btn>
              <v-btn
                v-if="adAccion==1"
                @click="activar()"
                color="orange darken-4"
                flat="flat"
              >Activar</v-btn>
              <v-btn
                v-if="adAccion==2"
                @click="desactivar()"
                color="orange darken-4"
                flat="flat"
              >Desactivar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
      <v-data-table :headers="headers" :items="articulos" :search="search" class="elevation-1">
        <template v-slot:items="props">
          <td class="justify-center layout px-0">
            <v-icon small class="mr-2" @click="editItem(props.item)">edit</v-icon>
            <template v-if="props.item.estado">
              <v-icon small @click="activarDesactivarMostrar(2,props.item)">block</v-icon>
            </template>
            <template v-else>
              <v-icon small @click="activarDesactivarMostrar(1,props.item)">check</v-icon>
            </template>
          </td>
          <td class="blue--text">{{ props.item.codigoBarras }}</td>
          <td>{{props.item.codigoInventario.descripcion}}</td>
          <td>{{ props.item.nombre }}</td>
          <td>{{ props.item.nombreComercial }}</td>
          <td class="green--text">{{ props.item.stock }}</td>
          <td class="green--text">{{ props.item.fraccionesTotales }}</td>
          <td>{{ props.item.fraccionCaja }}</td>
          <td class="red--text">{{ props.item.fechaCaducidad }}</td>
          <td>{{ props.item.costoNeto }}</td>

          <td>{{ props.item.pvp }}</td>
          <td>{{ props.item.precioUni }}</td>
          <td>{{ props.item.descuento }}</td>
          <td>
            <div v-if="props.item.iva">
              <span class="blue--text">Si</span>
            </div>
            <div v-else>
              <span class="red--text">No</span>
            </div>
          </td>
          <td>{{ props.item.codigoCategoria.nombre }}</td>
          <td>{{ props.item.codigoPresentacion.descripcion }}</td>
          <td>{{ props.item.codigoLaboratorio.abreviatura }}</td>
          <td>
            <div v-if="props.item.estado">
              <span class="blue--text">Activo</span>
            </div>
            <div v-else>
              <span class="red--text">Inactivo</span>
            </div>
          </td>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary" @click="listar()">RECARGAR</v-btn>
        </template>
      </v-data-table>
    </v-flex>
  </v-layout>
</template>


<script>
import axios from "axios";
import jsPDF from "jspdf";
import autoTable from "jspdf-autotable";
import Swal from 'sweetalert2'
export default {
  data() {
    return {
      modal: false,
      checkbox:false,
      dialog: false,
      search: "",
      farma: "",
      articulos: [],
      headers: [
        { text: "Opciones", value: "opciones", sortable: false },
        { text: "Codigo de Barras", value: "codigoBarras", sortable: false },
        { text: "Inventario", value: "codigoInventario", sortable: false },
        { text: "Producto", value: "nombre", sortable: true },
        { text: "Nombre comercial", value: "nombreComercial", sortable: false },
        { text: "Stock/Caja", value: "stock", sortable: false },
        {
          text: "Total/Fracciones",
          value: "fraccionesTotales",
          sortable: true
        },
        { text: "Fracciones/Caja", value: "fraccionCaja", sortable: false },
        { text: "Fecha caducidad", value: "fechaCaducidad", sortable: false },
        { text: "Costo neto", value: "costoNeto", sortable: false },
        { text: "PVP", value: "pvp", sortable: false },
        { text: "P. UNIT", value: "precioUni", sortable: false },
        { text: "Descuento", value: "descuento", sortable: false },
        { text: "IVA", value: "iva", sortable: false },
        { text: "Categoria", value: "codigoCategoria", sortable: false },
        { text: "Presentacion", value: "codigoPresentacion", sortable: false },
        { text: "Laboratorio", value: "codigoLaboratorio", sortable: false },
        { text: "Estado", value: "estado", sortable: false }
      ],
      editedIndex: -1,
      _id: "",

      codigoBarras: "",
      nombre: "",
      nombreComercial: "",
      stock: "",
      fraccionesTotales: "",
      fraccionCaja: "",
      fechaCaducidad: "",
      costoNeto: "",

      pvp: "",
      precioUni: "",
      descuento: "",
      iva: "",
      ivade: [
        { text: "SI", value: 1 },
        { text: "NO", value: 0 }
      ],
      codigoCategoria: "",
      categoria: [],
      codigoLaboratorio: "",
      laboratorio: [],
      codigoInventario: "",
      inventario: [],
      codigoPresentacion: "",
      presentacion: [],

      valida: 0,
      validaMensaje: [],
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: "",
      farmacias: [],
      Farmacia: ""
    };
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Nuevo registro" : "Editar registro";
    }
  },
  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  created() {
    this.listar();
    this.selectrInventario();
    this.selectCategoria();
    this.selectLaboratorio();
    this.selectPresentacion();
    this.ObtenerFa();
    // this.checkbox_Bandera();
  },
  methods: {
    checkbox_Bandera(){
        if (this.checkbox) {
          let me = this;
          let header = { Token: this.$store.state.token };
          let configuracion = { headers: header };
          let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;
           axios
          .get("productos/listtotal", configuracion)
          .then(function(response) {
            me.articulos = response.data;
          })
          .catch(function(error) {
            console.log(error);
          });
        }else{
          this.listar()
        }
    },
    crearPDF() {
      var img =
        "data:image/jpeg;base64,";

      var columns = [
        { title: "Codigo de Barras", dataKey: "codigoBarras" },
        { title: "Inventario", dataKey: "codigoInventario" },
        { title: "Producto", dataKey: "nombreComercial" },
        { title: "PVP", dataKey: "pvp" },
        { title: "Stock/Caja", dataKey: "stock" },
        { title: "Total/Fracciones", dataKey: "fraccionesTotales" },
        { title: "Laboratorio", dataKey: "codigoLaboratorio" }
      ];
      var rows = [];
      this.articulos.map(function(x) {
        rows.push({
          codigoBarras: x.codigoBarras,
          codigoInventario:x.codigoInventario.descripcion,
          nombreComercial: x.nombreComercial,
          pvp: x.pvp,
          stock: x.stock,
          fraccionesTotales: x.fraccionesTotales,
          codigoLaboratorio: x.codigoLaboratorio.abreviatura
        });
      });
      let me = this;
      var doc = new jsPDF();
      doc.autoTable(columns, rows, {
        margin: { top: 60 },
        addPageContent: function(data) {
          doc.text("Reporte de articulos", 80, 20);
          doc.setTextColor("blue");
          doc.text("LINEA DE FARMACIAS TU AHORRO", 60, 30);
          doc.text(me.Farmacia, 85, 40);
          doc.addImage(img, "JPEG", 160, 10, 35, 35);
        }
      });
      doc.save("ReporteArticulos.pdf");
    },
    ObtenerFa() {
      let me = this;
      let ArrayT = [];

      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;
      if (codigoFarmacia == undefined) {
        me.Farmacia = "ADMINISTRADOR";
      } else {
        axios
          .get("farmacia/query?_id=" + codigoFarmacia, configuracion)
          .then(function(response) {
            me.farmacias = response.data;
            me.Farmacia = me.farmacias.descripcion;
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    selectrInventario() {
      let me = this;
      let ArrayT = [];
      const codigo = "";
      let header1 = { Token: this.$store.state.token };
      let configuracion1 = { headers: header1 };
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;

      if (codigoFarmacia == undefined) {
        axios
          .get("inventario/list", configuracion)
          .then(function(response) {
            ArrayT = response.data;
            ArrayT.map(function(x) {
              me.inventario.push({ text: x.descripcion, value: x._id });
            });
          })
          .catch(function(error) {
            console.log(error);
          });
      } else {
        let me = this;
        let ArrayT = [];
        let header = { Token: this.$store.state.token };
        let configuracion = { headers: header };
        axios
          .get("inventario/query?_id=" + codigoFarmacia, configuracion)
          .then(function(response) {
            me.inventario.push({
              text: response.data.descripcion,
              value: response.data._id
            });
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    selectPresentacion() {
      let me = this;
      let ArrayT = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("presentacion/list", configuracion)
        .then(function(response) {
          ArrayT = response.data;
          ArrayT.map(function(x) {
            me.presentacion.push({ text: x.descripcion, value: x._id });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    selectCategoria() {
      let me = this;
      let ArrayCat = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("categoria/list", configuracion)
        .then(function(response) {
          ArrayCat = response.data;
          ArrayCat.map(function(x) {
            me.categoria.push({ text: x.nombre, value: x._id });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    selectLaboratorio() {
      let me = this;
      let LaboratorioArray = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("laboratorio/list", configuracion)
        .then(function(response) {
          LaboratorioArray = response.data;
          LaboratorioArray.map(function(x) {
            me.laboratorio.push({ text: x.nombre, value: x._id });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    listar() {
      let me = this;
      const codigo = "";
      let header1 = { Token: this.$store.state.token };
      let configuracion1 = { headers: header1 };
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;

      if (codigoFarmacia == undefined) {
        axios
          .get("productos/listtotal", configuracion)
          .then(function(response) {
            me.articulos = response.data;
           
          })
          .catch(function(error) {
            console.log(error);
          });
      } else {
        //Buscar el inventario a traves del codigo farmacia...
        axios
          .get("inventario/query?_id=" + codigoFarmacia, configuracion1) //#1
          .then(function(codigoInvenatio) {
            //Buscar los productos que pertenecen al inventario...
            axios
              .get(
                "productos/listB?valor=" + codigoInvenatio.data._id,
                configuracion
              )
              .then(function(response) {
                me.articulos = response.data;
              })
              .catch(function(error) {
                console.log(error);
              });
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    limpiar() {
      this._id = "";

      this.codigoBarras = "";
      this.nombre = "";
      this.nombreComercial = "";
      this.stock = "";
      this.fraccionesTotales = "";
      this.fraccionCaja = "";
      this.fechaCaducidad = "";
      this.costoNeto = "";

      this.pvp = "";
      this.precioUni = "";
      this.descuento = "";
      this.iva = "";
      this.codigoCategoria = "";
      this.codigoLaboratorio = "";
      this.codigoPresentacion = "";
      this.codigoInventario = "";

      this.valida = 0;
      this.validaMensaje = [];
      this.editedIndex = -1;
    },
    validar() {
      this.valida = 0;
      this.validaMensaje = [];
      if (!this.laboratorio) {
        this.validaMensaje.push("Selecciona una laboratorio.");
      }
      if (!this.categoria) {
        this.validaMensaje.push("Selecciona una categoria.");
      }
      if (!this.presentacion) {
        this.validaMensaje.push("Selecciona una presentacion.");
      }
      if (!this.inventario) {
        this.validaMensaje.push("Selecciona un inventario.");
      }

      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },
    guardar() {
      let me = this;
      if (this.validar()) {
        return;
      }
      if (this.editedIndex > -1) {
        //Código para editar
        axios
          .put("productos/update", {
            _id: this._id,

            codigoBarras: this.codigoBarras,
            nombre: this.nombre,
            nombreComercial: this.nombreComercial,
            stock: this.stock,
            fraccionesTotales: this.fraccionesTotales,
            fraccionCaja: this.fraccionCaja,
            fechaCaducidad: this.fechaCaducidad,
            costoNeto: this.costoNeto,

            pvp: this.pvp,
            precioUni: this.precioUni,
            descuento: this.descuento,
            iva: this.iva,
            codigoCategoria: this.codigoCategoria,
            codigoLaboratorio: this.codigoLaboratorio,
            codigoPresentacion: this.codigoPresentacion,
            codigoInventario: this.codigoInventario
          })
          .then(function(response) {
              Swal.fire('Nofiticación','Articulo editado!','success')
            me.limpiar();
            me.close();
            me.listar();
          })
          .catch(function(error) {
            console.log(error);
          });
      } else {
        //Código para guardar
        axios
          .post("productos/add", {
            codigoBarras: this.codigoBarras,
            nombre: this.nombre,
            nombreComercial: this.nombreComercial,
            stock: 0,
            fraccionesTotales: 0,
            fraccionCaja: this.fraccionCaja,
            fechaCaducidad: this.fechaCaducidad,
            costoNeto: this.costoNeto,

            pvp: this.pvp,
            precioUni: this.precioUni,
            descuento: this.descuento,
            iva: this.iva,
            codigoCategoria: this.codigoCategoria,
            codigoLaboratorio: this.codigoLaboratorio,
            codigoPresentacion: this.codigoPresentacion,
            codigoInventario: this.codigoInventario
          })
          .then(function(response) {
              Swal.fire('Nofiticación','Articulo guardado!','success')
            me.limpiar();
            me.close();
            me.listar();
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    editItem(item) {
      this._id = item._id;
      this.codigoBarras = item.codigoBarras;
      this.nombre = item.nombre;
      this.nombreComercial = item.nombreComercial;
      this.stock = item.stock;
      this.fraccionesTotales = item.fraccionesTotales;
      this.fraccionCaja = item.fraccionCaja;
      this.fechaCaducidad = item.fechaCaducidad;
      this.costoNeto = item.costoNeto;
      this.codigoCategoria = item.codigoCategoria._id;
      this.codigoLaboratorio = item.codigoLaboratorio._id;
      this.codigoPresentacion = item.codigoPresentacion._id;
      this.codigoInventario = item.codigoInventario._id;
      this.pvp = item.pvp;
      this.precioUni = item.precioUni;
      this.descuento = item.descuento;
      this.iva = item.iva;

      this.dialog = true;
      this.editedIndex = 1;
    },
    activarDesactivarMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.nombre;
      this.adId = item._id;
      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },
    activarDesactivarCerrar() {
      this.adModal = 0;
    },
    activar() {
      let me = this;
      axios
        .put("productos/activate", { _id: this.adId })
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    desactivar() {
      let me = this;
      axios
        .put("productos/deactivate", { _id: this.adId })
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    close() {
      this.dialog = false;
    }
  }
};
</script>
