<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>Cuentas por cobrar</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                 <v-btn class="success" @click="mostrarExportar">
          Fechas
        </v-btn>
        <v-btn @click="crearPDF()">
          <v-icon>print</v-icon>
        </v-btn>
                <v-spacer></v-spacer>
                <v-text-field class="text-xs-center" v-model="search" append-icon="search" 
                label="Búsqueda" single-line hide-details></v-text-field>
                <v-spacer></v-spacer>
                <v-dialog v-model="ExportarModal" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="headline">Seleccione el rango de fechas:</span>
              </v-card-title>
              <v-card-text>
                <v-container grid-list-md>
                  <v-layout wrap>
                          <v-flex xs12 sm6 md6>
                            <v-dialog
                              ref="dialog1"
                              v-model="modal1"
                              :return-value.sync="finicio"
                              persistent
                              width="290px"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                  <v-text-field
                                    v-model="finicio"
                                    label="Fecha de inicio"
                                    prepend-icon="event"
                                    readonly
                                    v-bind="attrs"
                                    v-on="on"
                                  ></v-text-field>
                                </template>
                                <v-date-picker v-model="finicio" scrollable>
                                  <v-spacer></v-spacer>
                                  <v-btn text color="primary" @click="modal1 = false">Cancelar</v-btn>
                                  <v-btn
                                    text
                                    color="success"
                                    @click="$refs.dialog1.save(finicio)"
                                  >Aceptar</v-btn>
                                </v-date-picker>
                              </v-dialog>
                          </v-flex>
                          <v-flex xs12 sm6 md6>
                            <v-dialog
                              ref="dialog"
                              v-model="modal"
                              :return-value.sync="ffin"
                              persistent
                              width="290px"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                  <v-text-field
                                    v-model="ffin"
                                    label="Fecha de fin"
                                    prepend-icon="event"
                                    readonly
                                    v-bind="attrs"
                                    v-on="on"
                                  ></v-text-field>
                                </template>
                                <v-date-picker v-model="ffin" scrollable>
                                  <v-spacer></v-spacer>
                                  <v-btn text color="primary" @click="modal = false">Cancelar</v-btn>
                                  <v-btn
                                    text
                                    color="success"
                                    @click="$refs.dialog.save(ffin)"
                                  >Aceptar</v-btn>
                                </v-date-picker>
                              </v-dialog>
                          </v-flex>
                            <v-flex xs12 sm12 md12 v-show="valida">
                              <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
                            </v-flex>
                  </v-layout>
                </v-container>
              </v-card-text>  
              <v-card-actions>
                  <v-btn color="primary" @click="filtrarFechas()">Buscar</v-btn>
                   <v-btn color="secundary" @click="ocultarExportar()">Cancelar</v-btn>
              </v-card-actions>
            </v-card>
        </v-dialog >
                <v-dialog v-model="dialog" max-width="500px">
                    
                    <v-card>
                        <v-card-title>
                        <span class="headline">{{ formTitle }}</span>
                        </v-card-title>            
                        <v-card-text>
                        <v-container grid-list-md>
                            <v-layout wrap>
                               
                                <v-flex xs12 sm12 md12>
                                    <v-text-field v-model="descripcion" label="Descripción"></v-text-field>
                                </v-flex>
                                <v-flex xs12 sm12 md12 v-show="valida">
                                    <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v">

                                    </div>
                                </v-flex>
                            </v-layout>
                        </v-container>
                        </v-card-text>            
                        <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" flat @click="close">Cancelar</v-btn>
                        <v-btn color="blue darken-1" flat @click="guardar">Guardar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
                <v-dialog v-model="adModal" max-width="290">
                    <v-card>
                        <v-card-title class="headline" v-if="adAccion==1">
                            Cambiar estado de Cuenta
                        </v-card-title>
                        <v-card-title class="headline" v-if="adAccion==2">
                            Pagar Cuenta
                        </v-card-title>
                        <v-card-text>
                            Estás a punto de <span v-if="adAccion==1">canlear </span>
                            <span v-if="adAccion==2">Pagar </span> la cuenta por cobrar de la factura #{{adNombre}}
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn @click="activarDesactivarCerrar()" color="green darken-1" flat="flat">
                                Cancelar
                            </v-btn>
                            <v-btn v-if="adAccion==1" @click="activar()" color="orange darken-4" flat="flat">
                                Por pagar
                            </v-btn>
                            <v-btn v-if="adAccion==2" @click="desactivar()" color="orange darken-4" flat="flat">
                                Pagar
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="rol"
                :search="search"
                class="elevation-1"
            >
                <template v-slot:items="props">
                <td class="justify-center layout px-0">
                   
                    <template v-if="props.item.estado">
                        <v-icon
                        small
                        @click="activarDesactivarMostrar(2,props.item)"
                        >
                          check
                        </v-icon>
                    </template>
                    <template v-else>
                        <v-icon
                        small
                        @click="activarDesactivarMostrar(1,props.item)"
                        >
                         block
                        </v-icon>
                    </template>
                </td>
                <td>{{ props.item.numComprobante }}</td>
                 <td>{{ formatearFecha(props.item.createdAt) }}</td>
                <td>{{ props.item.descripcion }}</td>
                <td>{{ props.item.plazo }}</td>
                <td>{{ props.item.tiempo }}</td>
                <td>{{ props.item.total }}</td>
                <td>{{ props.item.codigoUsuario.nombres }}</td>
                 <td>{{ props.item.codigoPersona.nombres }}</td>
                <td>
                    <div v-if="props.item.estado">
                        <span class="red--text">Por pagar</span>
                    </div>
                    <div v-else>
                        <span class="green--text">Pagado</span>
                    </div>
                </td>                
                </template>
                <template v-slot:no-data>
                <v-btn color="primary" @click="listar()">Resetear</v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>
    import moment from "moment"
    import axios from "axios";
    import jsPDF from "jspdf";
    import autoTable from "jspdf-autotable";
    import Swal from 'sweetalert2'
    export default {
    data() {
        return {
        finicio:"",
        ffin:"",
        modal:false,
        modal1:false,
        ExportarModal:0,
                dialog: false,
                search:'',
                rol:[],
                headers: [
                    { text: 'Opciones', value: 'opciones', sortable: false },
                    { text: 'Numero de comprobante', value: 'numComprobante', sortable: true },
                    {text:'Fecha',value:'craetedAt',sorteable:true},
                    { text: 'Factura', value: 'descripcion', sortable: false },
                    { text: 'Plazo', value: 'plazo', sortable: false },
                    { text: 'Tiempo', value: 'tiempo', sortable: false },
                    { text: 'Total', value: 'total', sortable: false },
                    { text: 'Usuario', value: 'codigoUsuario', sortable: false },
                    { text: 'Cliente', value: 'codigoPersona', sortable: false },
                    { text: 'Estado', value: 'estado', sortable: true },
                ],
                editedIndex: -1,
                _id:'',
                descripcion:'',
                valida:0,
                validaMensaje:[],
                adModal:0,
                adAccion:0,
                adNombre:'',
                adId:''
            }
        },
        computed: {
            formTitle () {
            return this.editedIndex === -1 ? 'Nuevo registro' : 'Editar registro'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            this.listar()
        },
        methods: {
             formatearFecha(value) {
      if (value) {
        return moment(String(value)).format('MM/DD/YYYY hh:mm')
      }
      },
            listar(){
                let me=this;
                let header={"Token":this.$store.state.token};
                let configuracion = {headers:header};
                axios.get('ctascobrar/list',configuracion).then(function (response){
                    me.rol=response.data;
                }).catch(function(error){
                    console.log(error);
                });

            },
            limpiar(){
                this._id='';
               
                this.descripcion='';
                this.valida=0;
                this.validaMensaje=[];
                this.editedIndex=-1;
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];
              
                if(this.descripcion.length>255){
                    this.validaMensaje.push('La descripción  no debe tener más de 255 caracteres.');
                }
                if (this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
            guardar(){
                let me=this;
                if (this.validar()){
                    return;
                }
                if (this.editedIndex >-1){
                    //Código para editar
                    axios.put('rol/update',{'_id':this._id,'descripcion':this.descripcion})
                    .then(function(response){
                        me.limpiar();
                        me.close();
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
                }else{
                    //Código para guardar
                    axios.post('rol/add',{'descripcion':this.descripcion})
                    .then(function(response){
                        me.limpiar();
                        me.close();
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
                }
            },
            editItem (item) {
                this._id=item._id;
               
                this.descripcion=item.descripcion;
                this.dialog = true;
                this.editedIndex=1;
            },
            activarDesactivarMostrar(accion,item){
                this.adModal=1;
                this.adNombre=item.descripcion;
                this.adId=item._id;
                if (accion==1){
                    this.adAccion=1;
                } else if(accion==2){
                    this.adAccion=2;
                } else{
                    this.adModal=0;
                }
            },
            activarDesactivarCerrar(){
                this.adModal=0;
            },
            activar(){
                let me=this;
                axios.put('ctascobrar/activate',{'_id':this.adId})
                    .then(function(response){
                        me.adModal=0;
                        me.adAccion=0;
                        me.adNombre='';
                        me.adId='';
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
            },
            desactivar(){
                let me=this;
                axios.put('ctascobrar/deactivate',{'_id':this.adId})
                    .then(function(response){
                        me.adModal=0;
                        me.adAccion=0;
                        me.adNombre='';
                        me.adId='';
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
            },
            close () {
                this.dialog = false;
            },
             validarF() {
      this.valida = 0;
      this.validaMensaje = [];
      
      var Ainicio = this.finicio.slice(0,4)
      
      var Minicio = this.finicio.slice(5,7)
     
      var Dinicio = this.finicio.slice(8,10)
      
      
      var Afin = this.ffin.slice(0,4)
      
      var Mfin = this.ffin.slice(5,7)
    
      var Dfin = this.ffin.slice(8,10)
      if(parseInt(Ainicio)<=parseInt(Afin) && parseInt(Minicio)<=parseInt(Mfin) && parseInt(Dinicio)<=parseInt(Dfin)){
         
      }else{
        this.validaMensaje.push("Debe ingresar una fecha de incio.");
      }
      if(!this.finicio) {
        this.validaMensaje.push("Debe ingresar una fecha de incio.");
      }
      if (!this.ffin) {
        this.validaMensaje.push("Debe ingresar una fecha de fin.");
      }
       if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },
    filtrarFechas(){
        let me = this;
          let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;
     if (this.validarF()) {
        return;
      }
       if (codigoFarmacia == undefined) {
        axios
          .get("ctascobrar/listporfechasad?fechainicio="+this.finicio+"&fechafin="+this.ffin+"")
         .then(function(response) {
          me.rol = response.data;
          me.ocultarExportar()
         })
        .catch(function(error) {
          console.log(error);
        });
      } 
      else {
         axios
          .get("ctascobrar/listporfechas?valor=" + codigoFarmacia+"&fechainicio="+this.finicio+"&fechafin="+this.ffin+"")
         .then(function(response) {
          me.rol = response.data;
          me.ocultarExportar()
         })
        .catch(function(error) {
          console.log(error);
        });
        }
          
    },
    mostrarExportar(item){
      this.ExportarModal=1;
    },
    ocultarExportar(item){
      this.finicio="";
      this.ffin="";
      this.validaMensaje=[]
      this.ExportarModal=0;
    },
    crearPDF() {
       let me = this;
      var img =
        "data:image/jpeg;base64,";

     
      
      var columns = [
        { title: "Numero de comprobante", dataKey:"numComprobante" },
        { title: "Detalle", dataKey:"descripcion" },
        { title: "Plazo", dataKey:"plazo"},
        { title: "Tiempo", dataKey:"tiempo" },
        { title: "Total",dataKey:"total"},
        { title: "Usuario",dataKey:"codigoUsuario"},
        { title: "Cliente",dataKey:"codigoPersona"},
        { title: "Fecha", dataKey: "createdAt"},
        ]
        /*headers: [
                   
                    { text: 'Numero de comprobante', value: 'numComprobante', sortable: true },
                    { text: 'Fecha',value:'craetedAt',sorteable:true},
                    { text: 'Factura', value: 'descripcion', sortable: false },
                    { text: 'Plazo', value: 'plazo', sortable: false },
                    { text: 'Tiempo', value: 'tiempo', sortable: false },
                    { text: 'Total', value: 'total', sortable: false },
                    { text: 'Usuario', value: 'codigoUsuario', sortable: false },
                    { text: 'Cliente', value: 'codigoPersona', sortable: false },
                    
                ] */
      var rows = [];
      this.rol.map(function(x) {
        rows.push({
          numComprobante: x.numComprobante,
          descripcion: x.descripcion,
          plazo: x.plazo,
          tiempo: x.tiempo,
          total: x.total,
          codigoUsuario: x.codigoUsuario.nombres,
          codigoPersona: x.codigoPersona.nombres,
          createdAt:me.formatearFecha(x.createdAt),
          
        });
      });
     
      var doc = new jsPDF();
      doc.autoTable(columns, rows, {
        margin: { top: 60 },
        addPageContent: function(data) {
          doc.text("Reporte de cuentas por cobrar", 70, 20);
          doc.setTextColor("blue");
          doc.text("LINEA DE FARMACIAS TU AHORRO", 60, 30);
          doc.text("Reporte", 95, 40);
          doc.addImage(img, "JPEG", 160, 10, 35, 35);
        }
      });
      doc.save("ReporteContable.pdf");
    },
        }
    }
</script>
