<template>
  <v-layout align-start>
    <v-flex>
      <v-toolbar flat color="white">
        <v-toolbar-title>Egresos</v-toolbar-title>
        <v-divider class="mx-2" inset vertical></v-divider>
        <v-btn class="success" @click="mostrarExportar">
          Fechas
        </v-btn>
        <v-btn @click="crearPDF()">
          <v-icon>print</v-icon>
        </v-btn>
        <v-spacer></v-spacer>
        <v-text-field
          v-if="verNuevo==0"
          class="text-xs-center"
          v-model="search"
          append-icon="search"
          label="Búsqueda"
          single-line
          hide-details
        ></v-text-field>
        <v-spacer></v-spacer>
            <v-btn color="orange" v-if="verNuevo==0" @click="mostrarNuevo()" dark class="mb-2">Pedir Autorizacion</v-btn>
            <v-dialog v-model="ExportarModal" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="headline">Seleccione el rango de fechas:</span>
              </v-card-title>
              <v-card-text>
                <v-container grid-list-md>
                  <v-layout wrap>
                          <v-flex xs12 sm6 md6>
                            <v-dialog
                              ref="dialog1"
                              v-model="modal1"
                              :return-value.sync="finicio"
                              persistent
                              width="290px"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                  <v-text-field
                                    v-model="finicio"
                                    label="Fecha de inicio"
                                    prepend-icon="event"
                                    readonly
                                    v-bind="attrs"
                                    v-on="on"
                                  ></v-text-field>
                                </template>
                                <v-date-picker v-model="finicio" scrollable>
                                  <v-spacer></v-spacer>
                                  <v-btn text color="primary" @click="modal1 = false">Cancelar</v-btn>
                                  <v-btn
                                    text
                                    color="success"
                                    @click="$refs.dialog1.save(finicio)"
                                  >Aceptar</v-btn>
                                </v-date-picker>
                              </v-dialog>
                          </v-flex>
                          <v-flex xs12 sm6 md6>
                            <v-dialog
                              ref="dialog"
                              v-model="modal"
                              :return-value.sync="ffin"
                              persistent
                              width="290px"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                  <v-text-field
                                    v-model="ffin"
                                    label="Fecha de fin"
                                    prepend-icon="event"
                                    readonly
                                    v-bind="attrs"
                                    v-on="on"
                                  ></v-text-field>
                                </template>
                                <v-date-picker v-model="ffin" scrollable>
                                  <v-spacer></v-spacer>
                                  <v-btn text color="primary" @click="modal = false">Cancelar</v-btn>
                                  <v-btn
                                    text
                                    color="success"
                                    @click="$refs.dialog.save(ffin)"
                                  >Aceptar</v-btn>
                                </v-date-picker>
                              </v-dialog>
                          </v-flex>
                            <v-flex xs12 sm12 md12 v-show="valida">
                              <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
                            </v-flex>
                  </v-layout>
                </v-container>
              </v-card-text>  
              <v-card-actions>
                  <v-btn color="primary" @click="filtrarFechas()">Buscar</v-btn>
                   <v-btn color="secundary" @click="ocultarExportar()">Cancelar</v-btn>
              </v-card-actions>
            </v-card>
        </v-dialog >
        <v-dialog v-model="dialog" max-width="1000px">
          <v-card>
            <v-card-title>
              <span class="headline">Seleccione un artículo</span>
            </v-card-title>
            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12 sm12 md12 lg12 xl12>
                    <v-text-field
                      v-model="texto"
                      @keyup.enter="listarArticulosnombres()"
                      class="text-xs-center"
                      append-icon="search"
                      label="Búsqueda"
                    ></v-text-field>
                    <template>
                      <v-data-table
                        :headers="cabeceraArticulos"
                        :items="articulos"
                        class="elevation-1"
                       >
                        <template v-slot:items="props">
                          <td class="justify-center layout px-0">
                            <v-icon small class="mr-2" @click="agregarDetalle(props.item)">add</v-icon>
                          </td>
                          <td class="blue--text">{{ props.item.codigoBarras }}</td>
                          <td>{{ props.item.nombre }}</td>
                          <td>{{ props.item.nombreComercial }}</td>
                          <td class="green--text">{{ props.item.codigoLaboratorio.abreviatura }}</td>
                          <td>{{ props.item.stock }}</td>
                          <td>{{ props.item.fraccionesTotales }}</td>
                          <td>${{ props.item.pvp}}</td>
                          <td>{{ props.item.descuento}}%</td>
                          <td>{{props.item.codigoInventario.descripcion}}</td>
                          
                          <td>
                            <div v-if="props.item.estado">
                              <span class="blue--text">Activo</span>
                            </div>
                            <div v-else>
                              <span class="red--text">Inactivo</span>
                            </div>
                          </td>
                        </template>
                      </v-data-table>
                    </template>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="close">Cancelar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="adModal" max-width="290">
          <v-card>
            <v-card-title class="headline" v-if="adAccion==1">Aprobar egreso</v-card-title>
            <v-card-title class="headline" v-if="adAccion==2">No aprobar</v-card-title>
            <v-card-text>
              Estás a punto de
              <span v-if="adAccion==1">Aprobar</span>
              <span v-if="adAccion==2">Anular</span>
              el egreso # {{adNombre}}
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn @click="activarDesactivarCerrar()" color="green darken-1" flat="flat">Cancelar</v-btn>
              <v-btn
                v-if="adAccion==1"
                @click="activar()"
                color="orange darken-4"
                flat="flat"
              >Activar</v-btn>
              <v-btn
                v-if="adAccion==2"
                @click="desactivar()"
                color="orange darken-4"
                flat="flat"
              >Desactivar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
      <v-data-table
        :headers="headers"
        :items="ingresos"
        :search="search"
        class="elevation-1"
        v-if="verNuevo==0"
         >
        <template v-slot:items="props">
          <td class="justify-center layout px-0">
            <v-icon small class="mr-2" @click="verIngreso(props.item)">tab</v-icon>
            <template v-if="props.item.estado">
              <!-- <v-icon small @click="activarDesactivarMostrar(2,props.item)">block</v-icon> -->
            </template>
            <template v-else>
              <v-icon small @click="activarDesactivarMostrar(1,props.item)">check</v-icon>
            </template>
          </td>
          <td>{{ props.item.codigoInventarioE.descripcion }}</td>
          <td>{{ props.item.codigoInventarioR.descripcion }}</td>
          <td>{{ props.item.codigoUsuario.nombres }}</td>
          <td>{{ props.item.descripcion }}</td>
          <td>{{ props.item.numComprobante }}</td>
          <td>{{formatearFecha(props.item.createdAt)}}</td>
      
          <td>
            <div v-if="props.item.estado">
              <span class="green--text">Aceptado</span>
            </div>
            <div v-else>
              <span class="orange--text">Pendiente</span>
            </div>
          </td>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary" @click="listar()">Resetear</v-btn>
        </template>
      </v-data-table>
      <v-container grid-list-sm class="pa-4 white" v-if="verNuevo">
        <v-layout row wrap>
             <v-flex xs12 sm12 md12 lg12 xl2 class="font-weight-bold green--text" style="font-size:20px">
        <span>Farmacia: {{Farmacia}}</span>
      </v-flex>
          <v-flex xs12 sm6 md6 lg6 xl6>
            <v-autocomplete :items="inventario" v-model="codigoInventario" label="Farmacia:"></v-autocomplete>
          </v-flex>
           <v-flex xs12 sm6 md6 lg6 xl6>
            <v-text-field v-model="descripcion" label="Descripcion"></v-text-field>
          </v-flex>
          <v-flex xs12 sm8 md8 lg8 x8>
            <v-text-field v-model="codigoBarras" label="Código" @keyup.enter="buscarCodigo()"></v-text-field>
          </v-flex>
          <v-flex xs12 sm2 md2 lg2 xl2>
            <v-btn small fab dark color="teal" @click="mostrarModalArticulos()">
              <v-icon dark>list</v-icon>
            </v-btn>
          </v-flex>
          <v-flex xs12 sm2 md2 lg2 xl2 v-show="errorArticulo">
            <div class="red--text" v-text="errorArticulo"></div>
          </v-flex>
          <v-flex xs12 sm12 md12 lg12 xl12>
            <template>
              <v-data-table
                :headers="cabeceraDetalles"
                :items="detalles"
                hide-actions
                class="elevation-1"
              >
               <template slot="items" slot-scope="props">
                  <td>
                    <v-icon small class="mr-2" @click="eliminarDetalle(detalles,props.item)">delete</v-icon>
                  </td>
                  <!-- <td class="text-xs-center green--text">{{ props.item.codigoInventario.descripcion }}</td> -->
                  <td class="text-xs-center blue--text">{{ props.item.codigoBarras }}</td>
                  <td class="text-xs-center blue--text">{{ props.item.producto }}</td>
                  <td class="text-xs-center green--text">
                    <v-text-field v-model="props.item.cantidad" type="number" ></v-text-field>
                  </td>
                   <td class="text-xs-center green--text" v-if="props.item.stock">
                    <v-text-field v-model="props.item.stock" readonly type="number"></v-text-field>
                  </td>
               
                 <td class="text-xs-center green--text">
                     <v-text-field v-model="props.item.fracciones"  type="number"></v-text-field>
                  </td>
                  <td class="text-xs-center blue--text" v-if="props.item.fraccionesTotales">
                    <v-text-field v-model="props.item.fraccionesTotales" readonly type="number"></v-text-field>
                  </td>
                
                 
                  
                </template>
                <template slot="no-data">
                  <h3>No hay artículos agregados al detalle.</h3>
                </template>
              </v-data-table>
            
            </template>
          </v-flex>
          <v-flex xs12 sm12 md12 v-show="valida">
            <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
          </v-flex>
          <v-flex xs12 sm12 md12 lg12 xl12>
            <v-btn color="blue darken-1" flat @click.native="ocultarNuevo()">Cancelar</v-btn>
            <v-btn color="success" v-if="verDetalle==0" @click.native="guardar()">Guardar</v-btn>
          </v-flex>
        </v-layout>
      </v-container>
    </v-flex>
  </v-layout>
</template>
<script>
import moment from "moment"
import axios from "axios";
import jsPDF from "jspdf";
import autoTable from "jspdf-autotable";
import Swal from 'sweetalert2'
export default {
  data() {
    return {
       finicio:"",
      ffin:"",
      modal:false,
      modal1:false,
      ExportarModal:0,
      dialog: false,
      search: "",
      ingresos: [],
      headers: [
        { text: "Opciones", value: "opciones", sortable: false },    
        { text: "Farmacia Emite", value: "codigoInventarioE" },
        { text: "Farmacia Recibe", value: "codigoInventarioR" },
        { text: "Usuario", value: "codigoUsuario.nombres", sortable: false },
        { text: "Descripcion", value: "descripcion", sortable: false },
        { text: "Número comprobante",value: "numComprobante",sortable: false},
        { text: "Fecha", value: "createdAt", sortable: false },
        { text: "Estado", value: "estado", sortable: false }
      ],
      _id: "",
      codigoInventario: "",
      inventario: [],
      codigoTipoComprobante: "",
      tiposcomprobantes: [],
      serieComprobante: "",
      numComprobante: "",
      codigo: "",
      cabeceraDetalles: [],
      detalles: [],
      verNuevo: 0,
      errorArticulo: null,
      total: 0.0,
      totalParcial: 0.0,
      totalImpuesto: 0.0,
      articulos: [],
      texto: "",
      cabeceraArticulos: [
        { text: "Seleccionar", value: "seleccionar", sortable: false },
        { text: "Código", value: "codigo", sortable: false },
        { text: "Nombre", value: "nombre", sortable: true },
        { text: "Nombre Comercial", value: "nombreComercial", sortable: true },
        { text: "Laboratorio",value: "codigoLaboratorio.abreviatura",sortable: true},
        { text: "Stock", value: "stock", sortable: false },
        {text:"Fracciones totales", value:"fraccionesTotales"},
     
        
        { text: "Farmacia", value: "codigoInventario", sortable: false },
        { text: "Estado", value: "estado", sortable: false }
      ],
      verDetalle: 0,
      valida: 0,
      validaMensaje: [],
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: "",
      codigoBarras: "",
      iva:0,
      descripcion:'',
      Farmacia:"",
      farmacias: [],
      invFar:"",
      Far:[],
      conteo:""
    };
  },
  computed: {
    calcularTotal: function() {
      let resultado = 0.0;
      for (var i = 0; i < this.detalles.length; i++) {
        if(this.detalles[i].iva==1){
             resultado =
          resultado + (this.detalles[i].cantidad * this.detalles[i].costoNeto*0.12) + (this.detalles[i].cantidad * this.detalles[i].costoNeto);
        }else{
        resultado =
          resultado + this.detalles[i].cantidad * this.detalles[i].costoNeto;
        }
      }
      return resultado;
    },
    caltularSubtotal: function() {
      let resultado = 0.0;
      for (var i = 0; i < this.detalles.length; i++) {
        
        resultado =
          resultado + this.detalles[i].cantidad * this.detalles[i].costoNeto;
        
      }
      return resultado;
    },
    calcularIva: function() {
      let resultado = 0.0;
      for (var i = 0; i < this.detalles.length; i++) {
        if(this.detalles[i].iva==1){
             resultado =
          resultado + (this.detalles[i].cantidad * this.detalles[i].costoNeto*0.12) ;
        }
      }
      return resultado;
    }, 
  },
  watch: {
  dialog(val) {
      val || this.close();
    }
  },
  created() {
    this.listar();
    this.selectInventario();
    this.selectTipocomprobante();
     // this.selectFarmacias();
    this.ObtenerFa();
    this.obtenerIDINV();
    this.contarEgresos()
    this.notificacion();
  },
  methods: {
    notificacion(){               
       Swal.fire('Notificación','Solo se puede hacer egresos de un producto a la vez\npor seguridad y políticas internas.','warning')
},
    contarEgresos(){
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
       axios
          .get("egreso/contarventas" , configuracion)
          .then(function(response) {
            me.conteo=response.data           
          })
          .catch(function(error) {
            console.log(error);
          });
    },
    obtenerIDINV(){
      let me = this;
      let ArrayT = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;
      if (codigoFarmacia == undefined) {
        me.invFar = "ADMINISTRADOR";
      } else {
        axios
          .get("inventario/query?_id=" + codigoFarmacia, configuracion)
          .then(function(response) {
            me.Far = response.data;
          
            me.invFar = me.Far._id;
            
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    formatearFecha(value) {
      if (value) {
        return moment(String(value)).format('MM/DD/YYYY hh:mm')
      }
      },
    ObtenerFa() {
      let me = this;
      let ArrayT = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;
      if (codigoFarmacia == undefined) {
        me.Farmacia = "ADMINISTRADOR";
      } else {
        axios
          .get("farmacia/query?_id=" + codigoFarmacia, configuracion)
          .then(function(response) {
            me.farmacias = response.data;
           
            me.Farmacia = me.farmacias.descripcion;
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    selectFarmacias() {
      let me = this;
      let ArrayT = [];
      let header = { Token: this.$store.state.token };
      let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;
      let configuracion = { headers: header };
       if (codigoFarmacia == undefined) {
        axios
          .get("farmacia/list", configuracion)
          .then(function(response) {
             ArrayT = response.data;
          ArrayT.map(function(x) {
            me.farmacias.push({ text: x.descripcion, value: x._id });
          });
        })
          .catch(function(error) {
            console.log(error);
          });
      } else {
      
      axios
        .get("farmacia/query?_id="+codigoFarmacia, configuracion)
        .then(function(response) {
           me.farmacias.push({ text: response.data.descripcion, value: response.data._id })
         
        })
        .catch(function(error) {
          console.log(error);
        });}
    },
    selectInventario() {
      let me = this;
      let personaArray = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("inventario/list", configuracion)
        .then(function(response) {
          personaArray = response.data;
          personaArray.map(function(x) {
            me.inventario.push({ text: x.descripcion, value: x._id });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    selectTipocomprobante() {
      let me = this;
      let tipocomprobanteArray = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("tipocomprobante/list", configuracion)
        .then(function(response) {
          tipocomprobanteArray = response.data;
          tipocomprobanteArray.map(function(x) {
            me.tiposcomprobantes.push({ text: x.descripcion, value: x._id });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    buscarCodigo() {
      let me = this;
      me.errorArticulo = null;
       let header1 = { Token: this.$store.state.token };
      let configuracion1 = { headers: header1 };
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
        let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;
     let codigoBarr = this.codigoBarras;

     if (codigoFarmacia == undefined) {
        
      axios
        .get("productos/query?codigoBarras=" + this.codigoBarras, configuracion)
        .then(function(response) {
          me.agregarDetalle(response.data);
        })
        .catch(function(error) {
          me.errorArticulo = "No existe el artículo.";
        });
      } else {
       //Buscar el inventario a traves del codigo farmacia...
        axios
          .get("inventario/query?_id=" + codigoFarmacia, configuracion1) //#1
          .then(function(codigoInvenatio) {
           
            //Buscar los productos que pertenecen al inventario...
            axios
        .get("productos/queryB?valor="+codigoInvenatio.data._id+"&codigoBarras="+codigoBarr, configuracion)
        .then(function(response) {
          me.agregarDetalle(response.data);
        })
        .catch(function(error) {
          me.errorArticulo = "No existe el artículo.";
        });

          })
          .catch(function(error) {
            console.log(error);
          });
      
      }




    },
    agregarDetalle(data) {
      this.errorArticulo = null;
      if(this.detalles.length==1){
         this.errorArticulo = "Solo puede ingresar un producto por Egreso."; 
      }
      else if (this.encuentra(data._id) == true) {
        this.errorArticulo = "El artículo ya ha sido agregado.";
      } else {
        this.cabeceraDetalles.push
        ({ text: "Borrar", value: "borrar", sortable: false },
        { text: "Codigo", value: "codigoBarras", sortable: false },
        { text: "Producto", value: "producto", sortable: false },
        { text: "Cantidad", value: "cantidad", sortable: false },
        { text: "Stock", value: "stock", sortable: false },
        { text: "Fraccion/Caja", value: "fracciones", sortable: false },
        { text: "Fracciones totales", value: "fraccionesTotales", sortable: false})
        this.detalles.push({
          _id: data._id,
          codigoBarras: data.codigoBarras,
          producto: data.nombreComercial,
          cantidad: 0,
          stock:data.stock,
          fracciones: data.fraccionCaja,
          fraccionesTotales:data.fraccionesTotales,
        });
        this.codigoBarras = "";
      }
    },
    encuentra(id) {
      let sw = 0;
      for (var i = 0; i < this.detalles.length; i++) {
        if (this.detalles[i]._id == id) {
          sw = true;
        }
      }
      return sw;
    },
    eliminarDetalle(arr, item) {
      let i = arr.indexOf(item);
      if (i != -1) {
        arr.splice(i, 1);
      }
    },
    listarArticulos() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("productos/list?valor=" + this.texto, configuracion)
        .then(function(response) {
          me.articulos = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    listarArticulosnombres() {

       let me = this;
      const codigo = "";
      let text = this.texto;
      let header1 = { Token: this.$store.state.token };
      let configuracion1 = { headers: header1 };
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;

      if (codigoFarmacia == undefined) {
