    verIngreso(item) {
      this.limpiar();
      this.descripcion=item.descripcion;
      this.codigoInventario=item.codigoInventarioR._id
      this.impuesto = item.impuesto;
      this.cabeceraDetalles.push
        ({ text: "Borrar", value: "borrar", sortable: false },
