    listar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;
      let configuracion = { headers: header };
      if (codigoFarmacia == undefined) {
         axios
        .get("egreso/list", configuracion)
        .then(function(response) {
          me.ingresos = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
      } else {
       
       
      }



    },
    limpiar() {
      this.cabeceraDetalles=[]
      this.descripcion=""
      this._id = "";
      this.codigoTipoComprobante = "";
      this.serieComprobante = "";
      this.numComprobante = "";
      this.impuesto = 0.12;
      this.codigoBarras = "";
      this.total = 0;
      this.totalParcial = 0;
      this.totalImpuesto = 0;
      this.detalles = [];
      this.verNuevo = 0;
      this.valida = 0;
      this.validaMensaje = [];
      this.verDetalle = 0;
    },
    validar() {
      this.valida = 0;
      this.validaMensaje = [];
  
      
      if (this.detalles.length <= 0) {
        this.validaMensaje.push("Ingrese al menos un artículo al detalle");
      }
      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },
    mostrarNuevo() {
      this.verNuevo = 1;
    },
    ocultarNuevo() {
      this.verNuevo = 0;
      this.limpiar();
    }, 
    EnviarInformacion(email,mensaje,asunto){
      let me = this;
         axios
          .post("email/correo", {
               'email':email,'mensaje':mensaje,'asunto':asunto     
          })
          .then(function(response) {
           
            })
          .catch(function(error) {
            console.log(error);
          });
    },
    guardar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      if (this.validar()) {
        return;
      }
      //Código para guardar
      axios
        .post(
          "egreso/add",
          {
            codigoUsuario: this.$store.state.usuario.codigoUsuario,
            codigoInventarioE:this.invFar,
            codigoInventarioR: this.codigoInventario,
            descripcion:this.descripcion,
            numComprobante: this.conteo,
            impuesto: this.calcularIva,
            total: this.total,
            detalles: this.detalles
          },
          configuracion
        )
        .then(function(response) {
                          Swal.fire('Notificación','Por favor este pendiente del correo,\npronto administrativo aceptara su pedido de egreso.','success')

          me.limpiar();
          me.close();
          me.listar();
          let correo = "tuahorrogi@gmail.com"
          let mensaje ="El dependiente de la Farmacia "+me.Farmacia+" ha pedido autorizacion para realizar un egreso.\nPor favor revise el modulo EGRESO para autorizar la salida de los productos."
          let asunto = "Notificacion - Pedido de autorizacion de EGRESO"
          me.EnviarInformacion(correo,mensaje,asunto);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    activarDesactivarMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.numComprobante;
      this.adId = item._id;
      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },
    activarDesactivarCerrar() {
      this.adModal = 0;
    },
    activar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      let farmaci=[]
      let correoFarmaciaR=""
        let farmacii=[]
      let correoFarmaciaE=""
      axios
        .put("egreso/deactivate", { _id: this.adId }, configuracion)
        .then(function(response) {
          axios
          .get("/inventario/queryInv?_id=" + response.data.codigoInventarioE, configuracion)
          .then(function(response) {
             farmacii = response.data;
              correoFarmaciaE = farmacii.detalleFarmacia.correo;
          })
          axios
          .get("/inventario/queryInv?_id=" + response.data.codigoInventarioR, configuracion)
          .then(function(response) {
              farmaci = response.data;
              correoFarmaciaR = farmaci.detalleFarmacia.correo;
              me.adModal = 0;
              me.adAccion = 0;
              me.adNombre = "";
              me.adId = "";
              me.listar();
              let correo = me.farmacias.correo
              let mensaje ="Se ha autorizado el egreso, Por favor proceda con la salida del egreso.\n\n\nRecuerde ningun producto puede salir de farmacia si no ha sido autorizado para salir.\n\nFARMACIAS TU AHORRO."
              let asunto = "Notificacion - Pedido de egreso AUTORIZADO"
              me.EnviarInformacion(correoFarmaciaE,mensaje,asunto);

              let mensaje2 ="La Farmacia  "+me.Farmacia+" ha realizado un egreso que ya ha sido autorizado, pronto sera recibido en su punto.\n\nEl egreso ha sido actualizado en su inventario.\n\n\nFARMACIAS TU AHORRO."
              let asunto2 = "Notificacion - EGRESO RECIBIDO"
              me.EnviarInformacion(correoFarmaciaR,mensaje2,asunto2);         
  
                Swal.fire('Notificación','Por favor comunicar a los dependientes de farmacia\nque se ha realizado exitosamente el egreso.','success')
        
          })
          .catch(function(error) {
            console.log(error);
          });
         
         
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    desactivar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .put("egreso/activate", { _id: this.adId }, configuracion)
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
          

        })
        .catch(function(error) {
          console.log(error);
        });
    },
    close() {
      this.dialog = false;
    },
    validarF() {
      this.valida = 0;
      this.validaMensaje = [];
      
      var Ainicio = this.finicio.slice(0,4)
      
      var Minicio = this.finicio.slice(5,7)
     
      var Dinicio = this.finicio.slice(8,10)
      
      
      var Afin = this.ffin.slice(0,4)
      
      var Mfin = this.ffin.slice(5,7)
    
      var Dfin = this.ffin.slice(8,10)
      if(parseInt(Ainicio)<=parseInt(Afin) && parseInt(Minicio)<=parseInt(Mfin) && parseInt(Dinicio)<=parseInt(Dfin)){
         
      }else{
        this.validaMensaje.push("Debe ingresar una fecha de incio.");
      }
      if(!this.finicio) {
        this.validaMensaje.push("Debe ingresar una fecha de incio.");
      }
      if (!this.ffin) {
        this.validaMensaje.push("Debe ingresar una fecha de fin.");
      }
       if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },
    filtrarFechas(){
        let me = this;
          let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;
     if (this.validarF()) {
        return;
      }
       if (codigoFarmacia == undefined) {
        axios
          .get("egreso/listporfechasad?fechainicio="+this.finicio+"&fechafin="+this.ffin+"")
         .then(function(response) {
          me.ingresos = response.data;
          me.ocultarExportar()
         })
        .catch(function(error) {
          console.log(error);
        });
      } 
      else {
         axios
          .get("egreso/listporfechas?valor=" + codigoFarmacia+"&fechainicio="+this.finicio+"&fechafin="+this.ffin+"")
         .then(function(response) {
          me.ingresos = response.data;
          me.ocultarExportar()
         })
        .catch(function(error) {
          console.log(error);
        });
        }
          
    },
    mostrarExportar(item){
      this.ExportarModal=1;
    },
    ocultarExportar(item){
      this.finicio="";
      this.ffin="";
      this.validaMensaje=[]
      this.ExportarModal=0;
    },
    crearPDF() {
       let me = this;
      var img =
        "data:image/jpeg;base64,";

     
      
      
      
      var columns = [
        { title: "Farmacia Emite", dataKey:"codigoInventarioE" },
        { title: "Farmacia Recibe", dataKey:"codigoInventarioR" },
        { title: "Usuario", dataKey:"codigoUsuario" },
        { title: "Descripcion", dataKey:"descripcion" },
        { title: "Número comprobante",dataKey:"numComprobante"},
        { title: "Fecha", dataKey: "createdAt"},
        ]
      var rows = [];
      this.ingresos.map(function(x) {
        rows.push({
          codigoInventarioE: x.codigoInventarioE.descripcion,
          codigoInventarioR: x.codigoInventarioR.descripcion,
          codigoUsuario: x.codigoUsuario.nombres,
          descripcion: x.descripcion,
          numComprobante: x.numComprobante,
          createdAt:me.formatearFecha(x.createdAt),
        });
      });
     
      var doc = new jsPDF();
      doc.autoTable(columns, rows, {
        margin: { top: 60 },
        addPageContent: function(data) {
          doc.text("Reporte de egresos", 80, 20);
          doc.setTextColor("blue");
          doc.text("LINEA DE FARMACIAS TU AHORRO", 60, 30);
          doc.text(me.Farmacia, 85, 40);
          doc.addImage(img, "JPEG", 160, 10, 35, 35);
        }
      });
      doc.save("ReporteEgresos.pdf");
    },
  }
};
