<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>Arqueos</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-btn class="success" @click="mostrarExportar">
          Fechas
        </v-btn>
        <v-btn @click="crearPDF()">
          <v-icon>print</v-icon>
        </v-btn>
                <v-spacer></v-spacer>
                <v-text-field class="text-xs-center" v-model="search" append-icon="search" 
                label="Búsqueda" single-line hide-details></v-text-field>
                <v-spacer></v-spacer>
                <v-dialog v-model="ExportarModal" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="headline">Seleccione el rango de fechas:</span>
              </v-card-title>
              <v-card-text>
                <v-container grid-list-md>
                  <v-layout wrap>
                          <v-flex xs12 sm6 md6>
                            <v-dialog
                              ref="dialog1"
                              v-model="modal1"
                              :return-value.sync="finicio"
                              persistent
                              width="290px"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                  <v-text-field
                                    v-model="finicio"
                                    label="Fecha de inicio"
                                    prepend-icon="event"
                                    readonly
                                    v-bind="attrs"
                                    v-on="on"
                                  ></v-text-field>
                                </template>
                                <v-date-picker v-model="finicio" scrollable>
                                  <v-spacer></v-spacer>
                                  <v-btn text color="primary" @click="modal1 = false">Cancelar</v-btn>
                                  <v-btn
                                    text
                                    color="success"
                                    @click="$refs.dialog1.save(finicio)"
                                  >Aceptar</v-btn>
                                </v-date-picker>
                              </v-dialog>
                          </v-flex>
                          <v-flex xs12 sm6 md6>
                            <v-dialog
                              ref="dialog"
                              v-model="modal"
                              :return-value.sync="ffin"
                              persistent
                              width="290px"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                  <v-text-field
                                    v-model="ffin"
                                    label="Fecha de fin"
                                    prepend-icon="event"
                                    readonly
                                    v-bind="attrs"
                                    v-on="on"
                                  ></v-text-field>
                                </template>
                                <v-date-picker v-model="ffin" scrollable>
                                  <v-spacer></v-spacer>
                                  <v-btn text color="primary" @click="modal = false">Cancelar</v-btn>
                                  <v-btn
                                    text
                                    color="success"
                                    @click="$refs.dialog.save(ffin)"
                                  >Aceptar</v-btn>
                                </v-date-picker>
                              </v-dialog>
                          </v-flex>
                            <v-flex xs12 sm12 md12 v-show="valida">
                              <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
                            </v-flex>
                  </v-layout>
                </v-container>
              </v-card-text>  
              <v-card-actions>
                  <v-btn color="primary" @click="filtrarFechas()">Buscar</v-btn>
                   <v-btn color="secundary" @click="ocultarExportar()">Cancelar</v-btn>
              </v-card-actions>
            </v-card>
        </v-dialog >
                <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on }" v-if="bandera==false">
                        <v-btn id="BtnNuevo" color="primary" dark class="mb-2" v-on="on">Nuevo</v-btn>
                    </template>
                    <v-card>
                        <v-card-title>
                        <span class="headline">{{ formTitle }}</span>
                        </v-card-title>            
                        <v-card-text>
                        <v-container grid-list-md>
                            <v-layout wrap>
                               
                                <v-flex xs12 sm12 md12>
                                    <v-text-field v-model="totalCaja" label="Total caja:"></v-text-field>
                                </v-flex>
                                <v-flex xs12 sm12 md12 v-show="valida">
                                    <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v">

                                    </div>
                                </v-flex>
                            </v-layout>
                        </v-container>
                        </v-card-text>            
                        <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" flat @click="close">Cancelar</v-btn>
                        <v-btn color="blue darken-1" flat @click="guardar">Guardar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
                <v-dialog v-model="adModal" max-width="290">
                    <v-card>
                        <v-card-title class="headline" v-if="adAccion==1">
                            Activar Item
                        </v-card-title>
                        <v-card-title class="headline" v-if="adAccion==2">
                            Desactivar Item
                        </v-card-title>
                        <v-card-text>
                            Estás a punto de <span v-if="adAccion==1">activar </span>
                            <span v-if="adAccion==2">desactivar </span> el item {{adNombre}}
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn @click="activarDesactivarCerrar()" color="green darken-1" flat="flat">
                                Cancelar
                            </v-btn>
                            <v-btn v-if="adAccion==1" @click="activar()" color="orange darken-4" flat="flat">
                                Activar
                            </v-btn>
                            <v-btn v-if="adAccion==2" @click="desactivar()" color="orange darken-4" flat="flat">
                                Desactivar
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="arqueo"
                :search="search"
                class="elevation-1"
                v-if="$store.state.usuario.codigoFarmacia === undefined"
                >
                <template v-slot:items="props">
                <td class="justify-center layout px-0">
                    <v-icon
                    small
                    class="mr-2"
                    @click="editItem(props.item)"
                    >
                    edit
                    </v-icon>
                    <template v-if="props.item.estado">
                        <v-icon
                        small
                        @click="activarDesactivarMostrar(2,props.item)"
                        >
                        block
                        </v-icon>
                    </template>
                    <template v-else>
                        <v-icon
                        small
                        @click="activarDesactivarMostrar(1,props.item)"
                        >
                        check
                        </v-icon>
                    </template>
                </td>
                <td v-if=" props.item.totalCaja ==  props.item.totalBK03" class="green--text">{{ props.item.codigoFarmacia.descripcion }}</td>
                <td v-else-if="props.item.totalCaja <=  props.item.totalBK03" class="red--text">{{ props.item.codigoFarmacia.descripcion }}</td>
                <td v-else-if=" props.item.totalCaja >=  props.item.totalBK03" class="orange--text">{{ props.item.codigoFarmacia.descripcion }}</td>

                <td v-if=" props.item.totalCaja ==  props.item.totalBK03" class="green--text">{{  props.item.codigoUsuario.nombres }}</td>
                <td v-else-if="props.item.totalCaja <=  props.item.totalBK03" class="red--text">{{  props.item.codigoUsuario.nombres }}</td>
                <td v-else-if=" props.item.totalCaja >=  props.item.totalBK03" class="orange--text">{{  props.item.codigoUsuario.nombres }}</td>

                <td>{{ props.item.seccion }}</td>
                <td>{{ formatearFecha(props.item.createdAt) }}</td>
                
                <td v-if=" props.item.totalCaja ==  props.item.totalBK03" class="green--text">{{  props.item.totalCaja }}</td>
                <td v-else-if="props.item.totalCaja <=  props.item.totalBK03" class="red--text">{{  props.item.totalCaja}}</td>
                <td v-else-if=" props.item.totalCaja >=  props.item.totalBK03" class="orange--text">{{  props.item.totalCaja }}</td>


                <td v-if=" props.item.totalCaja ==  props.item.totalBK03" class="green--text">{{ props.item.totalBK03 }}</td>
                <td v-else-if="props.item.totalCaja <=  props.item.totalBK03" class="red--text">{{  props.item.totalBK03}}</td>
                <td v-else-if=" props.item.totalCaja >=  props.item.totalBK03" class="orange--text">{{  props.item.totalBK03 }}</td>

                
                <td>
                    <div v-if="props.item.estado">
                        <span class="blue--text">Activo</span>
                    </div>
                    <div v-else>
                        <span class="red--text">Inactivo</span>
                    </div>
                </td>                
                </template>
                <template v-slot:no-data>
                <v-btn color="primary" @click="listar()">Resetear</v-btn>
                </template>
            </v-data-table>
            <div v-else style="margin-left:50px" >
                <br><br><br><br> 
                <span class="headline red--text" >No tiene autorizacion para ver los registros de arqueos.</span>
            </div>
        </v-flex>
    </v-layout>
</template>
<script>
   import moment from "moment"
import axios from "axios";
import jsPDF from "jspdf";
import autoTable from "jspdf-autotable";
import Swal from 'sweetalert2'
    export default {
        data(){
            return{
                finicio:"",
                ffin:"",
                modal:false,
                modal1:false,
                ExportarModal:0,
                dialog: false,
                search:'',
                arqueo:[],
                headers: [
                    { text: 'Opciones', value: 'opciones', sortable: false },
                    { text: 'Farmacia', value: 'codigoFarmacia.descripcion', sortable: true },
                    { text: 'Usuario', value: 'codigoUsuario.nombres', sortable: false },
                    { text: 'Seccion', value: 'seccion', sortable: true },
                    { text: 'Fecha', value: 'createdAt', sortable: true },
                    { text: 'Total caja', value: 'totalCaja', sortable: false },
                    { text: 'Total sistema', value: 'totalBK03', sortable: false },
                     { text: 'Estado', value: 'estado', sortable: false },
                ],
                editedIndex: -1,
                _id:'',
                totalCaja:'',
                totalBK03:'',
                codigoUsuario:'',
                codigoFarmacia:'',
                seccion:'',
                valida:0,
                validaMensaje:[],
                adModal:0,
                adAccion:0,
                adNombre:'',
                adId:'',
                bandera:false
            }
        },
        computed: {
            formTitle () {
            return this.editedIndex === -1 ? 'Nuevo registro' : 'Editar registro'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
             this.listar()
             this.totalVentaSeccion();
             this.VerificarArqueo();
        },
        methods: {
            VerificarArqueo(){
                var btnnuevo = document.getElementById("BtnNuevo");
                let me=this;
                let date_ob = new Date();
                // current date
                // adjust 0 before single digit date
                let date = ("0" + date_ob.getDate()).slice(-2);
                // current month
                let month = ("0" + (date_ob.getMonth() + 1)).slice(-2);
                // current year
                let year = date_ob.getFullYear();
                // current hours
                let hours = date_ob.getHours();
                // current minutes
                let minutes = date_ob.getMinutes();
                // current seconds
                let seconds = date_ob.getSeconds();
                // prints date in YYYY-MM-DD format
                let data = year + "-" + month + "-" + date;
                 this.codigoFarmacia=this.$store.state.usuario.codigoFarmacia;
                    this.codigoUsuario=this.$store.state.usuario.codigoUsuario;
                       if (this.codigoFarmacia != undefined) {  
                                //MANANA
                                if(hours>=7 && hours<=14){
                                     me.seccion="Matutina"
                                    let header={"Token":this.$store.state.token};
                                    let configuracion = {headers:header};
                                    
                                    axios.get('arqueo/existe?cf='+me.codigoFarmacia+'&cu='+me.codigoUsuario+'&fi='+data+' 07:00:00&ff='+data+' 15:00:00&sc='+me.seccion+''
                                    ,configuracion)
                                    .then(function (response){
                                     
                                        if (response.data.message=="existe") {
                                            
                                        me.bandera=true
                                            Swal.fire('Nofiticación','Ya hizo el arqueo de caja, Ya no puede realizar otro.','error')
                                        }else{
                                            Swal.fire('Recuerde','Solo puede ingresar un arqueo de caja.','warning')
                                        }
                                    }).catch(function(error){
                                        console.log(error);
                                    });  
                                //TARDE  
                                }else if(hours>=15 && hours<=23){
                                    me.seccion="Vespertina"
                                    let header={"Token":this.$store.state.token};
                                    let configuracion = {headers:header};
                                    
                                    axios.get('arqueo/existe?cf='+me.codigoFarmacia+'&cu='+me.codigoUsuario+'&fi='+data+' 15:00:00&ff='+data+' 23:00:00&sc='+me.seccion+''
                                    ,configuracion)
                                    .then(function (response){
                                        
                                        if (response.data.message=="existe") {
                                        me.bandera=true
                                            Swal.fire('Nofiticación','Ya hizo el arqueo de caja, Ya no puede realizar otro.','error')
                                        }else{
                                            Swal.fire('Recuerde','Solo puede ingresar un arqueo de caja.','warning')
                                        }
                                    }).catch(function(error){
                                        console.log(error);
                                    });      
                                    
                                        
                                }else{
                                    console.log('Horario excede los limites')
                                }
                       }else{
                            Swal.fire('Notificacion','Ha ingresado como administrador','success') 
                       }
                  
            },
            formatearFecha(value) {
                if (value) {
                return moment(String(value)).format('MM/DD/YYYY hh:mm')
                   }
            },
            totalVentaSeccion(){
              
                let me=this;
                let date_ob = new Date();
                // current date
                // adjust 0 before single digit date
                let date = ("0" + date_ob.getDate()).slice(-2);
                // current month
                let month = ("0" + (date_ob.getMonth() + 1)).slice(-2);
                // current year
                let year = date_ob.getFullYear();
                // current hours
                let hours = date_ob.getHours();
                // current minutes
                let minutes = date_ob.getMinutes();
                // current seconds
                let seconds = date_ob.getSeconds();
                // prints date in YYYY-MM-DD format
                let data = year + "-" + month + "-" + date;

                    //MANANA
                    if(hours>=7 && hours<=14){
                       
                                let header={"Token":this.$store.state.token};
                        let configuracion = {headers:header};
                        
                        axios.get('arqueo/suma?fi='+data+' 07:00:00&ff='+data+' 14:59:00',configuracion)
                        .then(function (response){
                                me.totalBK03=response.data;
                                
                            // console.log(response.data)
                        }).catch(function(error){
                            console.log(error);
                        });  
                    //TARDE  
                    }else if(hours>=15 && hours<=23){
                          let header={"Token":this.$store.state.token};
                        let configuracion = {headers:header};
                        
                        axios.get('arqueo/suma?fi='+data+' 15:01:00&ff='+data+' 23:00:00',configuracion)
                        .then(function (response){
                              me.totalBK03=response.data;
                             
                            // console.log(response.data)
                        }).catch(function(error){
                            console.log(error);
                        });      
                         
                            
                    }else{
                        console.log('Horario excede los limites')
                    }
                
                  
                 
            },
            listar(){
                let me=this;
                let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;
                let header={"Token":this.$store.state.token};
                let configuracion = {headers:header};
                 if (codigoFarmacia == undefined) {
                  axios.get('arqueo/list',configuracion).then(function (response){
                    me.arqueo=response.data;
                }).catch(function(error){ 
                    console.log(error);
                });      
                 }
                

            },
            limpiar(){
                this._id='';
                this.totalCaja='';
                this.valida=0;
                this.validaMensaje=[];
                this.editedIndex=-1;
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];
                if(!this.totalCaja){
                    this.validaMensaje.push('Debe ingresar el total de la caja.');
                }
                if (this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
            guardar(){
                let me=this;
                if (this.validar()){
                    return;
                }
                    this.codigoFarmacia=this.$store.state.usuario.codigoFarmacia;
                    this.codigoUsuario=this.$store.state.usuario.codigoUsuario;

                   
                   
                    var fecha = new Date();
                    var hora = fecha.getHours()
                    var minutos = fecha.getMinutes()
                    if(hora>=7 && hora<=14){
                        
                        me.seccion="Matutina";
                        
                    }else if(hora>=15 && hora<=23){
                            
                            me.seccion="Vespertina"
                            
                    }else{
                        console.log('Horario excede los limites')
                    }
                    //Código para guardar
                    axios.post('arqueo/add',{
                        'totalCaja':me.totalCaja,
                        'totalBK03':me.totalBK03,
                        'codigoUsuario':me.codigoUsuario,
                        'codigoFarmacia':me.codigoFarmacia,
                        'seccion':me.seccion
                        })
                    .then(function(response){
                        me.limpiar();
                        me.close();
                        me.listar();
                        me.VerificarArqueo();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
                
            },
            editItem (item) {
                this._id=item._id;
                this.descripcion=item.descripcion;
                this.dialog = true;
                this.editedIndex=1;
            },
            activarDesactivarMostrar(accion,item){
                this.adModal=1;
                this.adNombre=item.descripcion;
                this.adId=item._id;
                if (accion==1){
                    this.adAccion=1;
                } else if(accion==2){
                    this.adAccion=2;
                } else{
                    this.adModal=0;
                }
            },
            activarDesactivarCerrar(){
                this.adModal=0;
            },
            activar(){
                let me=this;
                axios.put('arqueo/activate',{'_id':this.adId})
                    .then(function(response){
                        me.adModal=0;
                        me.adAccion=0;
                        me.adNombre='';
                        me.adId='';
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
            },
            desactivar(){
                let me=this;
                axios.put('arqueo/deactivate',{'_id':this.adId})
                    .then(function(response){
                        me.adModal=0;
                        me.adAccion=0;
                        me.adNombre='';
                        me.adId='';
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
            },
            close () {
                this.dialog = false;
            },
    validarF() {
      this.valida = 0;
      this.validaMensaje = [];
      
      var Ainicio = this.finicio.slice(0,4)
      
      var Minicio = this.finicio.slice(5,7)
     
      var Dinicio = this.finicio.slice(8,10)
      
      
      var Afin = this.ffin.slice(0,4)
      
      var Mfin = this.ffin.slice(5,7)
    
      var Dfin = this.ffin.slice(8,10)
      if(parseInt(Ainicio)<=parseInt(Afin) && parseInt(Minicio)<=parseInt(Mfin) && parseInt(Dinicio)<=parseInt(Dfin)){
         
      }else{
        this.validaMensaje.push("Debe ingresar una fecha de incio.");
      }
      if(!this.finicio) {
        this.validaMensaje.push("Debe ingresar una fecha de incio.");
      }
      if (!this.ffin) {
        this.validaMensaje.push("Debe ingresar una fecha de fin.");
      }
       if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },
    filtrarFechas(){
        let me = this;
          let codigoFarmacia = this.$store.state.usuario.codigoFarmacia;
     if (this.validarF()) {
        return;
      }
       if (codigoFarmacia == undefined) {
        axios
          .get("arqueo/listporfechasad?fechainicio="+this.finicio+"&fechafin="+this.ffin+"")
         .then(function(response) {
          me.arqueo = response.data;
          me.ocultarExportar()
         })
        .catch(function(error) {
          console.log(error);
        });
      } 
      
          
    },
    mostrarExportar(item){
      this.ExportarModal=1;
    },
    ocultarExportar(item){
      this.finicio="";
      this.ffin="";
      this.validaMensaje=[]
      this.ExportarModal=0;
    },
    crearPDF() {
       let me = this;
      var img =
        "data:image/jpeg;base64,";

     
      
      
      
      var columns = [
        { title: "Farmacia", dataKey:"codigoFarmacia" },
        { title: "Usuario", dataKey:"codigoUsuario" },
        { title: "Seccion", dataKey:"seccion" },
        { title: "Fecha", dataKey: "createdAt"},
        { title: "Total caja", dataKey: "totalCaja"},
        { title: "Total sistema", dataKey: "totalBK03"},
        ]
        /*   headers: [
                   
                    { text: 'Farmacia', value: 'codigoFarmacia.descripcion', sortable: true },
                    { text: 'Usuario', value: 'codigoUsuario.nombres', sortable: false },
                    { text: 'Seccion', value: 'seccion', sortable: true },
                    { text: 'Fecha', value: 'createdAt', sortable: false },
                    { text: 'Total caja', value: 'totalCaja', sortable: false },
                    { text: 'Total sistema', value: 'totalBK03', sortable: false },
                    
                ], */
      var rows = [];
      this.arqueo.map(function(x) {
        rows.push({
          codigoFarmacia: x.codigoFarmacia.descripcion,
          codigoUsuario: x.codigoUsuario.nombres,
          seccion: x.seccion,
          createdAt:me.formatearFecha(x.createdAt),
          totalCaja: x.totalCaja,
          totalBK03: x.totalBK03,
        });
      });
     
      var doc = new jsPDF();
      doc.autoTable(columns, rows, {
        margin: { top: 60 },
        addPageContent: function(data) {
          doc.text("Reporte de arqueos", 80, 20);
          doc.setTextColor("blue");
          doc.text("LINEA DE FARMACIAS TU AHORRO", 60, 30);
          doc.text("Administrador", 85, 40);
          doc.addImage(img, "JPEG", 160, 10, 35, 35);
        }
      });
      doc.save("ReporteArqueos.pdf");
    },
        }
    }
</script>
