    crearPDF1(){
       var quotes = document.getElementById('factura1');
                html2canvas(quotes).then(function (canvas) {
                    var capImg = canvas.toDataURL('image/png');
                    var anchoIMG = 210;
                    var alturaPAG = 297;

                    var alturaIMG = canvas.height * anchoIMG / canvas.width;
                    var heightLeft = alturaIMG;
                    
                    var doc = new jsPDF('p', 'mm');
                    var posicion = 0;

                    doc.addImage(capImg, 'PNG', 0, posicion, anchoIMG, alturaIMG);
                    heightLeft -= alturaPAG;

                    while (heightLeft >= 0) {
                        posicion = heightLeft - alturaIMG;
                        doc.addPage();
                        doc.addImage(capImg, 'PNG', 0, posicion, anchoIMG, alturaIMG);
                        heightLeft -= alturaPAG;
                    }
                    
                   doc.save('ComprobanteVenta.pdf');
      });
          
    },
