    detalleXmlgenerate() {
      for (let index = 0; index < this.detalles.length; index++) {
        let cant = 0;
        let pu = 0;
        let totalsinimpuesto = 0;
        let codigoPorcent = 0;
        let calculoporcentual = 0;
        let descto = 0;
        let tarifa = 0;
        if (this.detalles[index].cantidad == 0) {
          cant = this.detalles[index].fracciones;
          pu = this.detalles[index].precioUni;
          totalsinimpuesto =
          (cant * pu - (cant * pu * this.detalles[index].descuento) / 100).toFixed(2);
          descto = ((cant * pu * this.detalles[index].descuento) / 100).toFixed(2);
        } else {
          cant = this.detalles[index].cantidad;
          pu = (this.detalles[index].precioVenta);
          totalsinimpuesto =
            (cant * pu - (cant * pu * this.detalles[index].descuento) / 100).toFixed(2);
          descto = ((cant * pu * this.detalles[index].descuento) / 100).toFixed(2);
        }
        if (this.detalles[index].iva != 0) {
          codigoPorcent = 2;
          calculoporcentual = (totalsinimpuesto * 0.12).toFixed(2);
          tarifa=12;
        } else {
          codigoPorcent = 0;
          calculoporcentual = 0;
          tarifa=0;
        }

        this.detalleXml[index] = [
          [
            {
              codigoPrincipal: this.detalles[index].codigoB,
              codigoAuxiliar: this.detalles[index].codigoB,
              descripcion: this.detalles[index].producto,
              cantidad: cant,
              precioUnitario: pu,
              descuento: descto,
              precioTotalSinImpuesto: totalsinimpuesto,
              impuestos:{
              impuesto: [
                {
                  codigo: "2",
                  codigoPorcentaje: codigoPorcent,
                  tarifa: tarifa,
                  baseImponible: totalsinimpuesto,
                  valor: calculoporcentual
                }
              ]
              }
            }
          ]
        ];
      }
    },
    guardarXml(
      //InfoTributaria
      razonSocial,
      nombreComercial,
      ruc,
      dirMatriz,
      estab, //lugar donde se hizo la venta
      secuencial,
      //InfoFactura
      dirEstablecimiento,
      tipoIdentificacionComprador,
      razonSocialComprador,
      identificacionComprador,
      direccionComprador,
      totalSinImpuestos,
      totalDescuento,
      //Total con impuestos
      codigo,
      codigoPorcentaje,
      baseImponible,
      tarifa,
      valor,
      //Total de la factura
      importeTotal,
      detallepag,
      //Datos adicionales del cliente
      emailCli,
      dirCli,
      telfCli,farmacia,fpago){
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };

      axios
        .post(
          "xml/xml",
          {
            infoTributaria: {
              razonSocial: razonSocial,
              nombreComercial: nombreComercial,
              ruc: ruc,
              dirMatriz: dirMatriz,
              estab: estab, //lugar donde se hizo la venta
              ptoEmi: "1",
              secuencial: secuencial
            },
            infoFactura: {
              dirEstablecimiento: dirEstablecimiento,
              //tipoIdentificacionComprador RUC=04,CI=05
              tipoIdentificacionComprador: tipoIdentificacionComprador,
              razonSocialComprador: razonSocialComprador,
              identificacionComprador: identificacionComprador,
              direccionComprador: direccionComprador,
              totalSinImpuestos: totalSinImpuestos,
              totalDescuento: totalDescuento,
              totalConImpuestos: {
                totalImpuesto: {
                  //codigo para iva 2
                  //codigoPorcentaje 0=0%,2=12%
                  codigo: codigo,
                  codigoPorcentaje: codigoPorcentaje,
                  baseImponible: baseImponible, //Valor sin el impuesto
                  tarifa:tarifa,
                  valor: valor //Valor del impuesto
                }
              },
              propina: "0.00",
              importeTotal: importeTotal //valor total de la venta
              ,pagos:{
                pago:detallepag
              }
            },
            detalles: {
              detalle: this.detalleXml
            },
            emailCli: emailCli,
            dirCli: dirCli,
            telfCli: telfCli,
            farmacia:farmacia,
            usuario:me.NombreUsuario,
            fpago:fpago
          },
          configuracion
        )
        .then(response => {
            let me = this;
          if (response.data) {
            let header = { Token: this.$store.state.token };
            let configuracion = { headers: header };
            let key=response.data[0];
            let num = response.data[1];
            axios
            .put("ventas/update",{'numComprobante':response.data[1],'clave':response.data[0]}, configuracion)
            .then(function(response){
              me.claveAc=key;
              let fecha = me.formatearFecha(moment().format());
              let farmacia=me.$store.state.usuario.codigoFarmacia.descripcion;
              let descuento = me.calcularDcto.toFixed(2);
              let iva = me.calcularIva.toFixed(2);
              var deta = [];
              let totality =0;
              for (let index = 0; index < me.detalles.length; index++) {
          
                let cant = 0;
                let pu = 0;
                let totalsinimpuesto = 0;
                let codigoPorcent = 0;
                let calculoporcentual = 0;
                let descto = 0;
                if (me.detalles[index].cantidad == 0) {
                  cant = me.detalles[index].fracciones;
                  pu = (me.detalles[index].precioUni);
                    totality=totality+(cant*pu);
                    totalsinimpuesto =
                    (cant * pu - (cant * pu * me.detalles[index].descuento) / 100).toFixed(2);
                  descto = ((cant * pu * me.detalles[index].descuento) / 100).toFixed(2);
                  } else {
                  cant = me.detalles[index].cantidad;
                  pu = (me.detalles[index].precioVenta);
                  totality=totality+(cant*pu);
                    totalsinimpuesto =
                      (cant * pu - (cant * pu * me.detalles[index].descuento) / 100).toFixed(2);
                    descto = ((cant * pu * me.detalles[index].descuento) / 100).toFixed(2);
                  }
                if (me.detalles[index].iva != 0) {
                  codigoPorcent = 2;
                  calculoporcentual = (totalsinimpuesto * 0.12).toFixed(2);
                  } else {
                  codigoPorcent = 0;
                  calculoporcentual = 0;
                }
                  var det=[]
                  det= [{ producto:me.detalles[index].producto,
                                  cantidad:cant,
                                  precio:pu,
                                  descuento:descto,
                                  total:totalsinimpuesto}];
                        deta.push(det)
              }
              me.crearPDFBK(key,num,fecha,farmacia,dirMatriz,dirEstablecimiento,direccionComprador,identificacionComprador,razonSocialComprador,deta,emailCli,telfCli,totality,descuento,iva,importeTotal);  
              
            })
            .catch(function(error) {
              console.log(error);
            });
          }else{
            console.log("Ocurrio un error en el backend...");
          }
        })
        .catch(e => {
          console.log(e);
        });
    },
