      for (let index = 0; index < this.detalles.length; index++) {
        if(this.detalles[index].cantidad==0){
           if(this.detalles[index].fracciones>0){
               if(this.detalles[index].fracciones>=this.detalles[index].fraccionesTotales)
            {
              this.validaMensaje.push("Fracciones Excede las Fracciones Totales.");
            }
           }else{
             this.validaMensaje.push("Fracciones no pueden ser negativas.");
          }
        }
        else{
          if(this.detalles[index].cantidad>0){
            if(this.detalles[index].cantidad>=this.detalles[index].stock )
            {
              this.validaMensaje.push("Cantidad Excede el Stock.");
            }
          }else{
             this.validaMensaje.push("Cantidad no puede ser negativa.");
          }
         
        }

      }
      
      if (!this.personas) {
        this.validaMensaje.push("Seleccione un proveedor.");
      }
      if (!this.tiposcomprobantes) {
        this.validaMensaje.push("Seleccione un tipo de comprobante.");
      }
      if (!this.numComprobante) {
        this.validaMensaje.push("Ingrese el número del comprobante.");
      }

      if (this.detalles.length <= 0) {
        this.validaMensaje.push("Ingrese al menos un artículo al detalle");
      }
      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },
