            identificacionComprador = me.clientes.numDocumento;
            direccionComprador = me.clientes.direccion;
            telefono = me.clientes.telefono;
            correo = me.clientes.email;
          }
          let porciento = "";
          if (me.calcularIva.toFixed(5) != 0) {
            porciento = "2";
            tarifa="12";
          } else {
            porciento = "0";
            tarifa=0;
          }
          let dcto = 0;
          let cant = 0;
          let pu = 0;
          for (let k = 0; k < me.detalles.length; k++) {
            if (me.detalles[k].descuento == 0) {
              dcto = (dcto + 0);
            } else {
              if (me.detalles[k].cantidad == 0) {
                cant = me.detalles[k].fracciones;
                pu = me.detalles[k].precioUni;
                dcto = (dcto + (cant * pu * me.detalles[k].descuento) / 100).toFixed(2);
              } else {
                cant = me.detalles[k].cantidad;
                pu = me.detalles[k].precioVenta;
                dcto = (dcto + (cant * pu * me.detalles[k].descuento) / 100).toFixed(2);
              }
            }
            
            
            
            axios
              .get("inventario/queryinv?_id=" + me.detalles[k].codigoInventario._id)
