                        +"PRODUCTO    "+"   "+" CANTIDAD  "+"  CODIGO BARRAS \n"
                        +me.detalles[k].producto+"       --     "+me.detalles[k].cantidad+"    --      "+me.detalles[k].codigoB+
                        "\nPerteneciente a su punto.\nDentro de unos minutos el Cliente "+razonSocialComprador+" con identificacion "+identificacionComprador+" pasara por su punto retirando. Este a la espera;\n\nGracias por su paciencia, tenga un buen dia.\n\n\n\n\n Sistema BK03 punto de Venta.";
                        me.EnviarInformacion(email,mensaje,asunto);
                      })
                      .catch(function(error) {
                        console.log(error);
                      });
                      
                   
                }
              })
              .catch(function(error) {
                console.log(error);
              }); 
            
          }
          let detallePago=[]
          //CONDICIONAL FORMAS DE PAGOS
          //Hasta el momento solo se puede pagos en efectivo y creditos.
          if (me.fpago=="Efectivo") {
