            detallePago=[[
              {
              formaPago:'01',
              total:me.calcularTotal.toFixed(2)     
              }]
                ];
          }else{
            //CONDICIONAR FORMAS DE PAGO CON SUS VALORES
            if(me.row=="tcr"){
              formaparapago="TARJETA DE CRÉDITO"
                detallePago=[[
              {
                //CONDICIONAR FORMA DE PAGO
              // formaPago:'01',
              // total:me.calcularTotal.toFixed(2),
              // plazo:me.credito_plazo,
              // unidadTiempo:'dias'     
              }]
                ];
              }else if(me.row=="tdb"){
                formaparapago="TARJETA DE DÉBITO"
                detallePago=[[
              {
                //CONDICIONAR FORMA DE PAGO
              // formaPago:'01',
              // total:me.calcularTotal.toFixed(2),
              // plazo:me.credito_plazo,
              // unidadTiempo:'dias'     
              }]
                ];
              }
              else if(me.row=="cre"){
              formaparapago="CRÉDITO"
              detallePago=[[
              {
              formaPago:'01',
              total:me.calcularTotal.toFixed(2),
              plazo:me.credito_plazo,
              unidadTiempo:'dias'     
              }]
                ];
              }else if(me.row=="con"){
                if(me.chckCr && me.chckEfectivo){
                  formaparapago="CONSOLIDADO EF-CR"
                   detallePago=[[
                      {
                        formaPago:'01',
                        total:me.Efec_Val,  
                        }
                        ,
                        {
                        formaPago:'01',
                        total:me.credito_Val,
                        plazo:me.credito_Tiem,
                        unidadTiempo:'dias'     
