      this.adId = item._id;
      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },
    activarDesactivarCerrar() {
      this.adModal = 0;
    },
    activar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .put("ventas/activate", { _id: this.adId }, configuracion)
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
          var email="tuahorrogi@gmail.com"
          var mensaje="Se aprobó la factura número: "+me.obj.numComprobante+" con CA/NA: "+me.obj.claveAcceso+",\nemitida al cliente "+me.obj.codgioPersona.nombres+"\n con número de identificación "+me.obj.codgioPersona.numDocumento+" en la farmacia "+me.obj.codigoFarmacia.descripcion+".\nPara mas información contáctese con la persona a cargo del punto de venta."
          var asunto="Notificación de aprobación de factura."
          me.EnviarInformacion(email,mensaje,asunto);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    desactivar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      
      axios
        .put("ventas/deactivate", { _id: this.adId }, configuracion)
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          var email="tuahorrogi@gmail.com"
          var mensaje="Se solicitó la anulación del comprobante número: "+me.obj.numComprobante+" con CA/NA: "+me.obj.claveAcceso+",\nemitida el:"+me.obj.createdAt+" al cliente "+me.obj.codgioPersona.nombres+"\ncon número de identificación "+me.obj.codgioPersona.numDocumento+" en la farmacia "+me.obj.codigoFarmacia.descripcion+".\nPara mas información contáctese con la persona a cargo del punto de venta."
          var asunto="Solicitud de anulación de comprobantes electronicos."
          me.EnviarInformacion(email,mensaje,asunto);
          me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    EnviarInformacion(email,mensaje,asunto){
      let me = this;
         axios
          .post("email/correo", {
               'email':email,'mensaje':mensaje,'asunto':asunto     
          })
          .then(function(response) {
           
            })
          .catch(function(error) {
            console.log(error);
          });
    },
    close() {
      this.dialog = false;
    }
  }
};
